<template>
    <div style="postion: relative;">
        <p-button
            v-if="hasNewMessage"
            :label="messageCount.toString()"
            icon="pi pi-bell"
            :class="buttonClass"
            @click="gotoMessageInbox"
        />
    </div>
</template>

<script>
import { inject, onMounted, onUnmounted, ref } from "vue";
import { emitter } from "@/event-bus";
import { useRoute, useRouter } from "vue-router";
import { $axios } from "@/service/app-http";

const BUTTON_CLASS = {
    warnig: "p-button-warning",
    danger: "p-button-danger"
};

export default {
    setup() {
        const router = useRouter();
        const hasNewMessage = ref(false);
        const messageCount = ref(0);
        const buttonClass = ref(BUTTON_CLASS.warnig);

        const websocket = inject("websocket");

        const setupSocket = () => {
            websocket.onmessage = (event) => {
                const data = JSON.parse(event.data);
                if (typeof data.data == "string") {
                    const message = JSON.parse(data.data);
                    if (message.terminal == "nlp") {
                        getUnReadCount();
                    }
                }
            };
        };

        const gotoMessageInbox = () => {
            router.push("/message-inbox");
        };

        const messageSeenHander = () => {
            hasNewMessage.value = false;
            messageCount.value = 0;
        };

        const getUnReadCount = () => {
            $axios
                .get("patient-msg-log/unread-count")
                .then((resp) => {
                    messageCount.value = resp.data.totUnread;
                    if (resp.data.totUnread > 0) {
                        hasNewMessage.value = true;
                        buttonClass.value = BUTTON_CLASS.danger;
                        setTimeout(() => {
                            buttonClass.value = BUTTON_CLASS.warnig;
                        }, 500);
                    }
                })
                .catch((err) => {});
        };

        onMounted(() => {
            setupSocket();

            websocket.onopen = () => {
                setupSocket();
            };

            emitter.on("message-nofication:has-seen", messageSeenHander);
            emitter.on("message-inbox:get-all-messgae", getUnReadCount);
            getUnReadCount();
        });

        onUnmounted(() => {
            emitter.off("message-nofication:has-seen", messageSeenHander);
            emitter.off("message-inbox:get-all-messgae", getUnReadCount);
        });

        return {
            hasNewMessage,
            messageCount,
            gotoMessageInbox,
            buttonClass
        };
    }
};
</script>

<style scoped>
.p-badge {
}
</style>
