import * as semenCryoStorageService from "@/service/semen-cryo-storage";

const state = () => ({
    providers: null,
    semenCryoStorage: null
});

const actions = {
    async getProviders({ state, commit }) {
        try {
            if (!state.providers) {
                const data = await semenCryoStorageService.getProviders();
                commit("SET_PROVIDERS", data);
                return data;
            }
            return Promise.resolve();
        } catch (err) {
            return Promise.reject();
        }
    },
    async getSemenCryoStorage({ commit }, cycleId) {
        try {
            const data = await semenCryoStorageService.getSemenCryoStorage(
                cycleId
            );
            commit("SET_SEMEN_CYRO_STORAGE", data);
            return Promise.resolve();
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async saveSemenCryoStorage({ commit }, payload) {
        try {
            const data = semenCryoStorageService.save(payload);
            return Promise.resolve();
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async deleteSemenCryoStorage({ commit }, id) {
        try {
            const data = semenCryoStorageService.deleteSemenCryoStorage(id);
            commit("SET_SEMEN_CYRO_STORAGE", null);
            return Promise.resolve();
        } catch (err) {
            return Promise.reject();
        }
    }
};

const mutations = {
    SET_SEMEN_CYRO_STORAGE(state, semenCryoStorage) {
        state.semenCryoStorage = semenCryoStorage;
    },
    SET_PROVIDERS(state, providers) {
        state.providers = providers;
    }
};

const getters = {};

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
};
