import { createStore } from "vuex";

import AuthService from "../service/auth.js";
import options from "@/store/options.js";
import artCycle from "@/store/art-cycle";
import cycleMonitoring from "@/store/cyccle-monitoring";
import opu from "@/store/opu";
import semen from "@/store/semen";
import pregnancy from "@/store/pregnancy";
import born from "@/store/born";
import utils from "@/store/utils";
import semenCryoStorage from "@/store/semen-cryo-storage";
import cycleReport from "@/store/cycle-report";
import fertilization from "@/store/fertilization";
import doctor from "@/store/doctor";
import embryoTransfer from "@/store/embryo-transfer";
import provider from "@/store/provider";
import cryoStorage from "@/store/cryo-storage.js";
import dailyReport from "@/store/daily-report";
import bloodTest from "@/store/blood-test";
import users from "@/store/users";
import maintanance from "@/store/maintanance";

export default createStore({
    state: () => {
        const user = JSON.parse(localStorage.getItem("user"));
        const verified = false;
        const needSecondVerification = JSON.parse(localStorage.getItem("needSecondVerification"));

        return {
            user,
            verified,
            needSecondVerification
        };
    },
    actions: {
        login({ state, commit }, user) {
            return AuthService.login(user).then(
                (data) => {
                    commit("loginSuccess");
                    return Promise.resolve(data);
                },
                (error) => {
                    commit("loginFailure");
                    return Promise.reject(error);
                }
            );
        },
        logout({ commit }) {
            AuthService.logout();
            commit("logout");
        },
        register({ commit }, user) {
            return AuthService.register(user).then(
                (response) => {
                    commit("registerSuccess");
                    return Promise.resolve(response.data);
                },
                (error) => {
                    commit("registerFailure");
                    return Promise.reject(error);
                }
            );
        }
    },
    mutations: {
        loginSuccess(state) {
            state.user = JSON.parse(localStorage.getItem("user"));
        },
        loginFailure(state) {
            state.user = null;
        },
        logout(state) {
            state.user = null;
        },
        registerSuccess(state) {
            state.status.loggedIn = false;
        },
        registerFailure(state) {
            state.status.loggedIn = false;
        },
        setVerified(state) {
            state.verified = true;
        }
    },
    modules: {
        artCycle,
        options,
        cycleMonitoring,
        opu,
        semen,
        pregnancy,
        born,
        utils,
        semenCryoStorage,
        cycleReport,
        fertilization,
        embryoTransfer,
        doctor,
        provider,
        cryoStorage,
        dailyReport,
        bloodTest,
        users,
        maintanance
    }
});
