import { $axios } from "./app-http";

export async function getTotalPatient(fromDate, toDate) {
    const {
        data: { totalPatients }
    } = await $axios.get("daily-report/get-total-patients", {
        params: { fromDate, toDate }
    });
    return { totalPatients };
}

export async function getTotalOrders(fromDate, toDate) {
    const {
        data: { totalOrders }
    } = await $axios.get("daily-report/get-total-orders", {
        params: { fromDate, toDate }
    });
    return { totalOrders };
}

export async function getCycleData(autoTestOrderId) {
    const { data } = await $axios.get(
        "daily-report/get-cycle-data-by-blood-test-id",
        {
            params: { autoTestOrderId }
        }
    );
    return data;
}

export async function getPersonalCycle(testId) {
    const { data } = await $axios.get("daily-report/get-personal-cycles", {
        params: { testId }
    });
    return data;
}

export async function getOrderList(fromDate, toDate, providerNo = null) {
    const {
        data: { ordersList }
    } = await $axios.get("daily-report/get-orders-list", {
        params: { fromDate, toDate, providerNo }
    });
    return ordersList;
}

export async function assignCycleONUnassignedTests(payload) {
    const { data } = await $axios.post(
        "daily-report/assign-latestcycle-to-unassigned-blood-tests",
        payload
    );
    return data;
}

export async function getBhCGList(fromDate, toDate, providerNo) {
    try {
        const {
            data: { ordersList }
        } = await $axios.get("bhcg/get-orders-list", {
            params: { fromDate, toDate, providerNo }
        });
        return ordersList;
    } catch (error) {
        return [];
    }
}

export async function getPendingOrderList(fromDate, toDate, providerNo) {
    const { data } = await $axios.get("daily-report/get-pending-orders-list", {
        params: { fromDate, toDate, providerNo }
    });
    return data;
}

export async function assignCycleToBloodTest(payload) {
    const { data } = await $axios.post(
        "daily-report/assign-cycle-to-blood-test",
        payload
    );
    return data;
}

export async function deassignCycleToBloodTest(id) {
    const { data } = await $axios.post(
        "daily-report/deassign-cycle-from-blood-test",
        {
            testOrderId: id
        }
    );
    return data;
}

export async function saveCheckedSigleRecord(payload) {
    const { data } = await $axios.post(
        "daily-report/save-checked-single-records",
        payload
    );

    return data;
}

export async function getFundedBloodBillingList(params) {
    const {
        data: { fundedBloodBilling }
    } = await $axios.get("blood-billing/list-funded-blood-billing", {
        params: { ...params }
    });

    return fundedBloodBilling;
}

export async function getBloodBillingList(params) {
    const {
        data: { bloodBillingList }
    } = await $axios.get("blood-billing/list-blood-billing", {
        params: { ...params }
    });

    return bloodBillingList;
}

export async function pushToOscar(payload) {
    const { data } = await $axios.post(
        "blood-billing/push-selected-item-to-oscar",
        payload
    );
    return data;
}

export async function pushAllToOscar(payload) {
    const { data } = await $axios.post(
        "blood-billing/push-multiple-item-to-oscar",
        payload
    );
    return data;
}
