import { $axios } from "./app-http";

export async function getFertilizationByCycleId(cycleId) {
    const {
        data: { fertilization }
    } = await $axios.get("fertilization/by-cycle-id", { params: { cycleId } });

    return fertilization;
}

export async function createFertlization(payload) {
    const { data } = await $axios.post(
        "fertilization/create-fertilization",
        payload
    );
    return data;
}

export async function updateFertlization(payload) {
    const { data } = await $axios.post(
        "fertilization/save-fertilization",
        payload
    );
    return data;
}

export async function getFertilizationLine(fertId) {
    const {
        data: { fertilizationLines }
    } = await $axios.get("fertilization/fertilization-lines", {
        params: { fertId }
    });
    return fertilizationLines;
}

export async function createFertilizationLine(payload) {
    const {
        data: { fertLine }
    } = await $axios.post("fertilization-embryo-details/save-embryo", {
        ...payload
    });
    return fertLine;
}

export async function editFertilizationLine(payload) {
    const {
        data: { fertLine }
    } = await $axios.post("fertilization-embryo-details/save-embryo-multi", {
        ...payload
    });
    return fertLine;
}

export async function deleteFertilizationLine(payload) {
    const { data } = await $axios.post(
        "fertilization/delete-fertilization-line",
        payload
    );
    return data;
}

export async function deleteFertilizationLines(payload) {
    const { data } = await $axios.post(
        "fertilization/delete-fertilization-line-multi",
        {
            lineIds: payload
        }
    );
    return data;
}

export async function copyFertilizationLine(fertId, howMany) {
    const { data } = await $axios.post("copy-to-create/new-line", {
        fertId,
        howMany
    });
    return data;
}

export async function getOocyteEmbryoOptions() {
    const {
        data: { maturityList }
    } = await $axios.get("fertilization/maturity-list");
    const {
        data: { mediaInventoryList }
    } = await $axios.get("media_inventory/list");

    const {
        data: { stageList }
    } = await $axios.get("fertilization/stage-list");

    const {
        data: { gradeList }
    } = await $axios.get("fertilization/grade-list");

    return { maturityList, stageList, gradeList, mediaInventoryList };
}


export async function getMediaInventoryList() {
    const {
        data: { mediaInventoryList }
    } = await $axios.get("media_inventory/list");

    return { mediaInventoryList };
}

export async function saveFertlizationEmbryoDetail(payload) {
    const { data } = await $axios.post(
        "fertilization-embryo-details/save-details-freezer-v3",
        {
            ...payload
        }
    );

    return data;
}

export async function getFertlizationEmbryoDetail(oocId) {
    const {
        data: { fertilizationEmbryoDetails }
    } = await $axios.get("fertilization-embryo-details/get-details-v3", {
        params: {
            oocId
        }
    });
    return fertilizationEmbryoDetails;
}

export async function getFertlizationEmbryoDetailFromStart(lineId, dayNo) {
    const {
        data: { fertilizationEmbryoDetails }
    } = await $axios.get(
        "fertilization-embryo-details/get-details-from-start",
        {
            params: {
                dayNo: dayNo,
                lineId: lineId
            }
        }
    );
    return fertilizationEmbryoDetails;
}

export async function saveFertilizaionFollAspiration(payload) {
    const { data } = await $axios.post(
        "fertilization/save-fertilization-header",
        payload
    );
    return data;
}

export async function getPrimaryCycles(patientId, cycleDate) {
    try {
        const { data } = await $axios.get("born/get-ivf-cycles", {
            params: { cycleDate: cycleDate, patientId: patientId }
        });

        return data;
    } catch (err) {
        Promise.resolve([]);
    }
}

export async function updatePrimaryCycle(payload) {
    try {
        const { data } = await $axios.post(
            "born/update-primary-cycle",
            payload
        );

        return Promise.resolve({ status: "success" });
    } catch (err) {
        return Promise.resolve({ status: "fail" });
    }
}

export async function getCycleTypes() {
    try {
        const { data } = await $axios.get("fert/cycle-types");
        return data.data;
    } catch (err) {
        return Promise.resolve([]);
    }
}

export async function getAllFreezers() {
    try {
        const { data } = await $axios.get("cryo/equipment/list-arr", {
            params: { site: 1 }
        });
        return data;
    } catch (err) {
        return Promise.resolve([]);
    }
}

export async function getFreezerItmes(params) {
    try {
        const { data } = await $axios.get("cryo/locations", {
            params: { ...params }
        });
        return data;
    } catch {
        return Promise.resolve([]);
    }
}
