import * as providerServcie from "@/service/provider";

const state = () => ({
    providers: [],
    locations: []
});

const actions = {
    async getProviders({ state }) {
        try {
            if (state.providers.length === 0) {
                const data = await providerServcie.getProviders();
                state.providers = data;
            }
            return Promise.resolve();
        } catch (err) {
            return Promise.reject();
        }
    },
    async getProviderLocations({ state }) {
        try {
            if (state.locations.length <= 2) {
                const data = await providerServcie.getLocationOption();
                state.locations = data;
            }
            return Promise.resolve();
        } catch (err) {
            return Promise.reject();
        }
    }
};

export default {
    namespaced: true,
    state,
    actions
};
