import { $axios } from "./app-http";

const CYCLE_MONITORING_DETAIL_ENDPOINT = "cycle/monitoring-details";
const DRUG_1_OPTIONS_ENDPOINT = "monitor/drug1-list";
const DRUG_2_OPTIONS_ENDPOINT = "monitor/drug2-list";
const PROTOCOL_OPTIONS_ENDPOINT = "monitor/protocol-list";
const DIAGNOSIS_OPTIONS_ENDPOINT = "monitor/diagnosis-list";
const CREATE_NEW_MONITOR_LINE_ENDPOINT = "monitor/create-line";
const UPDATE_MONITOR_LINE_ENDPOINT = "monitor/update-line";
const UPDATE_COMMMENT_ENDPIONT = "monitor/save-signed-comment";
const SAVE_MONITORING_SUMMARY_ENDPOINT = "monitor/save-summary";

export async function getCycleMonitoringDetails(cycleId) {
    const { data } = await $axios.get(CYCLE_MONITORING_DETAIL_ENDPOINT, {
        params: {
            cycleId
        }
    });

    return data;
}

export async function getDaignosisOptions() {
    const {
        data: { diagnosis }
    } = await $axios.get(DIAGNOSIS_OPTIONS_ENDPOINT);
    return diagnosis;
}

export async function getProtocolOptions() {
    const {
        data: { protocols }
    } = await $axios.get(PROTOCOL_OPTIONS_ENDPOINT);
    return protocols;
}

export async function getDrug1Options() {
    const {
        data: { drugs1 }
    } = await $axios.get(DRUG_1_OPTIONS_ENDPOINT);
    return drugs1;
}

export async function getDrug2Options() {
    const {
        data: { drugs2 }
    } = await $axios.get(DRUG_2_OPTIONS_ENDPOINT);
    return drugs2;
}

export async function createMonitorLine(formData) {
    return await $axios.post(CREATE_NEW_MONITOR_LINE_ENDPOINT, formData);
}

export async function updateMonitorLine(formData) {
    return await $axios.post(UPDATE_MONITOR_LINE_ENDPOINT, formData);
}

export async function updateComment(formData) {
    return await $axios.post(UPDATE_COMMMENT_ENDPIONT, formData);
}

export async function saveMonitoringSummary(formData) {
    return await $axios.post(SAVE_MONITORING_SUMMARY_ENDPOINT, formData);
}

export async function deleteCycleMonitorLine(monitorLineId, patientId, user) {
    const { data } = await $axios.post("monitor/delete-line", {
        monLineId: monitorLineId,
        curPatientId: patientId,
        curUser: user
    });
    return data;
}

export async function getMonitorLineDeleteLog(fromDate, toDate, patientId) {
    const {
        data: { deleteLog }
    } = await $axios.get("monitor/get-delete-log", {
        params: { fromDate, toDate, patientId }
    });
    return deleteLog;
}

export async function restoreMonitorLine(cmLineId) {
    const { data } = await $axios.post("monitor/restore-monitor-line", {
        cmdLineId: cmLineId
    });
    return data;
}

export async function getInventoryList() {
    const { data } = await $axios.get("medicatio_inventory/list");
    return data;
}

export async function saveInvetory(payload) {
    const { data } = await $axios.post("medicatio_inventory/create", payload);
    return data;
}

export async function updateInvetory(payload) {
    const { data } = await $axios.post(
        `medicatio_inventory/update/${payload.id}`,
        payload
    );
    return data;
}

export async function deleteInventory(payload) {
    const { data } = await $axios.post(
        `medicatio_inventory/delete/${payload.id}`, payload
    );
    return data;
}

export async function linkWithBloodTest(monitorLineId) {
    try {
        const { data } = await $axios.post(
            `monitor-line/${monitorLineId}/link-blood-test`
        );
        return data;
    } catch (error) {
        return Promise.resolve({
            status: "error_occurred",
            message: "Somethis went wrong"
        });
    }
}
