import * as pregnancyService from "@/service/pregnancy";

const state = () => ({
    pregnancyDetails: null,
    fetusHistories: null,
    pregnancyTests: null,
    liveBirthHisotory: null
});

const actions = {
    async getPregnancyDetails({ commit }, cycleId) {
        try {
            const data = await pregnancyService.getPregnancyDetails(cycleId);
            commit("SET_PREGNANCY_DETAILS", data);

            return Promise.resolve();
        } catch (error) {
            return Promise.reject(error);
        }
    },
    async createNewPregnancy({ commit }, { cycleId, patientId }) {
        try {
            const data = await pregnancyService.createNew({
                cycleId,
                patientId
            });
            return Promise.resolve();
        } catch (error) {
            return Promise.reject(error);
        }
    },
    async savePregnancyOutcome({ commit }, formData) {
        try {
            const data = await pregnancyService.savePregnancyOutcome(formData);
            return Promise.resolve();
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async createNewFetusHistory({ dispatch, state }, formData) {
        try {
            const data = await pregnancyService.createNewFetusHistory(formData);
            return dispatch(
                "getFetusHistory",
                state.pregnancyDetails.pregnancyId
            );
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async getFetusHistory({ commit }, pregnancyId) {
        try {
            const data = await pregnancyService.getFetusHistory(pregnancyId);
            commit("SET_FETUS_HISTORIES", data);
            return data;
        } catch (err) {
            Promise.reject(err);
        }
    },
    async deleteFetus({ commit }, fetusHistoryId) {
        try {
            const data = await pregnancyService.deleteFetus(fetusHistoryId);
            return data;
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async updateFetus({ commit }, formData) {
        try {
            const data = await pregnancyService.updateFetus(formData);
            return data;
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async getLiveBirthHistory({ commit }, pregnancyId) {
        try {
            const data = await pregnancyService.getLiveBirthHisory(pregnancyId);
            commit("SET_LIVE_BIRTH_HISTORY", data);
            return data;
        } catch (err) {
            commit("SET_LIVE_BIRTH_HISTORY", []);
            Promise.reject(err);
        }
    },
    async createLiveBirth({ dispatch, state }, formData) {
        try {
            const data = await pregnancyService.createLiveBirth(formData);
            return dispatch(
                "getLiveBirthHistory",
                state.pregnancyDetails.pregnancyId
            );
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async deletLiveBirthHistory({ commit }, liveBirthId) {
        try {
            const data = await pregnancyService.deletLiveBirthHistory(
                liveBirthId
            );
            return data;
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async updateLiveBirth({ commit }, formData) {
        try {
            const data = await pregnancyService.updateLiveBirth(formData);
            return data;
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async deletePregnancy({ state, rootState }) {
        try {
            const data = await pregnancyService.deletePregnancy(
                rootState.artCycle.selectedCycle.cycleId
            );
            state.pregnancyDetails = null;
            return Promise.resolve();
        } catch {
            return Promise.reject();
        }
    }
};

const getters = {};

const mutations = {
    SET_PREGNANCY_DETAILS(
        state,
        { pregnancyDetails, fetusHistories, pregnancyTests, liveBirthHisotory }
    ) {
        state.pregnancyDetails = pregnancyDetails;
        state.fetusHistories = fetusHistories;
        state.pregnancyTests = pregnancyTests;
        state.liveBirthHisotory = liveBirthHisotory;
    },
    SET_FETUS_HISTORIES(state, fetusHistories) {
        state.fetusHistories = fetusHistories;
    },
    SET_LIVE_BIRTH_HISTORY(state, liveBirthHisotory) {
        state.liveBirthHisotory = liveBirthHisotory;
    },
    UPDATE_FETUS(state, { index, data }) {
        state.fetusHistories[index] = data;
    },
    CANCEL_FETUS_UPDATE(state, { index, data }) {
        state.fetusHistories[index] = data;
    },
    CANCEL_LIVE_BIRTH_UPDATE(state, { index, data }) {
        state.liveBirthHisotory[index] = data;
    }
};

export default {
    namespaced: true,
    state,
    actions,
    getters,
    mutations
};
