import { $axios } from "./app-http";

const DEMOGRAPHIC_SEARCH_ENDPOINT = "demographics/search";
const FORM_FIELD_OPTIONS_ENDOINT = "art-cycle/field-options";
const PATIENT_CYCLES_ENDPOINT = "art-cycle/cycles";
const CYCLE_SUMMARY_DETAIL = "cycles/summary-details";
const CYCLE_CREATE_ENDPOINT = "cycle/create-new";
const CYCLE_DELETE_ENDPOINT = "cycle/delete-cycle";
const CYCLE_UPDATE_ENDPOINT = "cycle/save-cycle";
const BORN_CYCLE_CREATE = "born/save-cycle-info";

export default {
    async searchDemographic({ term, sex }) {
        let limit = 10;
        return await $axios.get(DEMOGRAPHIC_SEARCH_ENDPOINT, {
            params: {
                term: term,
                sex: sex
            }
        });
    },
    async getFormFieldOptions() {
        return await $axios.get(FORM_FIELD_OPTIONS_ENDOINT);
    },
    async getCycles(patientId) {
        return await $axios.get(PATIENT_CYCLES_ENDPOINT, {
            params: {
                "patient-id": patientId
            }
        });
    },
    async getCycleSummaryDetail(patientId) {
        return await $axios.get(CYCLE_SUMMARY_DETAIL, {
            params: {
                patientId
            }
        });
    }
};

export async function searchDemographic({ term, sex }) {
    const data = await $axios.get(DEMOGRAPHIC_SEARCH_ENDPOINT, {
        params: {
            term: term,
            sex: sex
        }
    });

    return data;
}

export async function getFormFieldOptions() {
    const { data } = await $axios.get(FORM_FIELD_OPTIONS_ENDOINT);
    return data;
}

export async function getCycleSummaryDetail(patientId) {
    const {
        data: { data }
    } = await $axios.get(CYCLE_SUMMARY_DETAIL, {
        params: {
            patientId
        }
    });

    return data;
}

export async function createNewCycle(cycle) {
    const { data } = await $axios.post(CYCLE_CREATE_ENDPOINT, cycle);
    return data;
}

export async function deleteCycle(cycleId) {
    return await $axios.post(CYCLE_DELETE_ENDPOINT, {
        cycleId: parseInt(cycleId)
    });
}

export async function updateCycle(cycle) {
    const { data } = await $axios.post(CYCLE_UPDATE_ENDPOINT, cycle);
    return data;
}

export async function craetNewBorn(formData) {
    const { data } = await $axios.post(BORN_CYCLE_CREATE, formData);
    return data;
}

export async function mergeCycle({ cycle1, cycle2, dryRun }) {
    const {
        data: { cycleMergeResult }
    } = await $axios.post("cycle/merge-cycle", {
        cycleId1: cycle1,
        cycleId2: cycle2,
        isDryRun: dryRun
    });

    return cycleMergeResult;
}

export async function getCouplesReport(patientId) {
    const { data } = await $axios.get("embryo-storage/couples-report", {
        params: { patientId }
    });
    return data;
}

export async function getBornCSV(patientId, cycleIds) {
    const { data } = await $axios.get("born/generate-csv-v3", {
        params: { patientId, cycleIds }
    });
    return data;
}

export async function getBronCsvMulti(paylaod) {
    try {
        const { data } = await $axios.post("born/generate-csv-v3", paylaod);
        return data;
    } catch (err) {
        return Promise.resolve({ status: "failed", csv: null });
    }
}

export async function markBornAsCompleteService(patientId, cycleId) {
    const { data } = await $axios.post("born/mark-as-complete", {
        patientId,
        cycleId
    });
    return data;
}

export async function markBornAsUncompleteService(patientId, cycleId) {
    const { data } = await $axios.post("born/mark-as-uncomplete", {
        patientId,
        cycleId
    });
    return data;
}

export async function validateCsv(patientId, cycleIds) {
    const { data } = await $axios.get("born/csv-validator", {
        params: { patientId, cycleIds }
    });
    return data;
}

export async function getBornCompleteList(
    page,
    perPage,
    uploaded,
    cycleYear,
    cycleMonth,
    source = [],
    cycleCancelled,
    cycleType = []
) {
    try {
        const { data } = await $axios.get("born/completed-list", {
            params: {
                perPage,
                uploaded,
                page,
                source,
                cycleYear,
                cycleMonth,
                cycleCancelled,
                cycleType
            }
        });
        // console.log(data);
        return {
            data: data.data,
            total: data.total
        };
    } catch (err) {
        return Promise.resolve({
            data: [],
            total: 0
        });
    }
}

export async function getBornUncompleteList(
    page,
    perPage,
    cycleYear,
    cycleMonth,
    source = [],
    cycleCancelled,
    cycleType = []
) {
    try {
        const { data } = await $axios.get("born/un-completed-list", {
            params: {
                perPage,
                page,
                cycleYear,
                cycleMonth,
                source,
                cycleCancelled,
                cycleType
            }
        });
        // console.log(data);
        return {
            data: data.data,
            total: data.total
        };
    } catch (err) {
        return Promise.resolve({
            data: [],
            total: 0
        });
    }
}

export async function markAsUpload(paylaod) {
    try {
        const { data } = await $axios.post(
            "born/update-upload-status",
            paylaod
        );
        return Promise.resolve();
    } catch (err) {
        return Promise.resolve();
    }
}
