import { NL_FORM_BASE } from "@/constants";
import axios from "axios";
import { $axios } from "./app-http";

export async function getBloodTestResults(patientId) {
    const {
        data: { btdisplay }
    } = await $axios.get("blood-test/display-blood-tests-list", {
        params: { patientId }
    });
    return btdisplay;
}

export async function getBloodGroupTests() {
    const {
        data: { bloodGroups }
    } = await $axios.get("blood-test/get-blood-groups");
    return bloodGroups;
}

export async function addBloodTestOrderItems(payload) {
    const { data } = await $axios.post(
        "blood-test/add-blood-test-order-items",
        payload
    );
    return data;
}

export async function getBloodTest(testOrderId) {
    const {
        data: { btdisplay }
    } = await $axios.get("blood-test/display-blood-tests", {
        params: { testOrderId }
    });
    return btdisplay;
}

export async function addBloodTest(payload) {
    const { data } = await $axios.post(
        "blood-test/add-blood-tests",
        payload
    );

    return data;
}

export async function saveAllBloodTest(payload) {
    const { data } = await $axios.post(
        "blood-test/update-blood-tests",
        payload
    );

    return data;
}

export async function deleteResult(testOrderId) {
    const { data } = await $axios.post("blood-test/delete-blood-test-result", {
        testOrderId
    });

    return data;
}

export async function deleteOrder(testOrderId) {
    const { data } = await $axios.post("blood-test/delete-blood-test-all", {
        testOrderId
    });

    return data;
}

export async function cancelBloodTest(testOrderId, testCode) {
    const { data } = await $axios.post("blood-test/cancele-blood-test", {
        testOrderId,
        testCode
    });

    return data;
}

export async function printBarcodePDF(id) {
    let url = `${NL_FORM_BASE}/nlforms/gnpdf/?landscape=1&ps=custom-25.4-50.8&m0=1&m1=1&htmurl=${NL_FORM_BASE}/cycles-reports/print-barcode-pdf.php?testOrderId=${id}`;

    const {
        data: { pdf }
    } = await axios.get(url);

    return pdf;
}
