import { $axios } from "./app-http";

const GET_PREGNANCY_DETAILS_EDNPOINT = "pregnancy/get-details";
const CREATE_NEW_ENDPOINT = "pregnancy/create-pregnancy";
const SAVE_PREGNANCY_ENDPOINT = "pregnancy/save-pregnancy";
const CRATE_FETUS_HISTORY_ENDPOINT = "pregnancy/create-fetus-history";
const GET_FETUS_HISTORY_ENDPOINT = "pregnancy/get-fetus-list";
const DELETE_FETUS_HISTORY_ENDPOINT = "pregnancy/delete-fetus";
const UPDATE_FETUS_HISTORY_ENPOINT = "pregnancy/save-fetus";
const GET_LIVE_BIRTH_HISTORY_ENDPOINT = "pregnancy/get-births-list";
const CREATE_LIVE_BIRTH_ENDPOINT = "pregnancy/create-birth";
const DELETE_LIVE_BIRTH_ENPOINT = "pregnancy/delete-birth";
const UPDATE_LIVE_BIRTH_ENDPOINT = "pregnancy/edit-birth";

export async function getPregnancyDetails(cycleId) {
    const { data } = await $axios.get(GET_PREGNANCY_DETAILS_EDNPOINT, {
        params: { cycleId }
    });
    return data;
}

export async function createNew({ cycleId, patientId }) {
    const { data } = await $axios.post(CREATE_NEW_ENDPOINT, {
        cycleId,
        patientId
    });
    return data;
}

export async function savePregnancyOutcome(formData) {
    const { data } = await $axios.post(SAVE_PREGNANCY_ENDPOINT, formData);
    return data;
}

export async function createNewFetusHistory(formData) {
    const { data } = await $axios.post(CRATE_FETUS_HISTORY_ENDPOINT, formData);
    return data;
}

export async function getFetusHistory(pregnancyId) {
    const {
        data: { fetusesList }
    } = await $axios.get(GET_FETUS_HISTORY_ENDPOINT, {
        params: { pregnancyId }
    });
    return fetusesList;
}

export async function deleteFetus(fetusHistoryId) {
    const { data } = await $axios.post(DELETE_FETUS_HISTORY_ENDPOINT, {
        fetusHistoryId
    });
    return data;
}

export async function updateFetus(formData) {
    const { data } = await $axios.post(UPDATE_FETUS_HISTORY_ENPOINT, formData);
    return data;
}

export async function getLiveBirthHisory(pregnancyId) {
    const {
        data: { birthsList }
    } = await $axios.get(GET_LIVE_BIRTH_HISTORY_ENDPOINT, {
        params: { pregnancyId }
    });
    return birthsList;
}

export async function createLiveBirth(formData) {
    const { data } = await $axios.post(CREATE_LIVE_BIRTH_ENDPOINT, formData);
    return data;
}

export async function deletLiveBirthHistory(liveBirthHistoryId) {
    const { data } = await $axios.post(DELETE_LIVE_BIRTH_ENPOINT, {
        liveBirthHistoryId
    });
    return data;
}

export async function updateLiveBirth(formData) {
    const { data } = await $axios.post(UPDATE_LIVE_BIRTH_ENDPOINT, formData);
    return data;
}

export async function deletePregnancy(cycleId) {
    const { data } = await $axios.post("pregnancy/delete-pregnancy", {
        cycleId
    });
    return data;
}
