import { $axios } from "./app-http";

export async function getCryoStorageList(cycleId) {
    const {
        data: { data }
    } = await $axios.get("embryo-storage/list", { params: { cycleId } });
    return data;
}

export async function getCyroStorageByPatient(patientId) {
    const {
        data: { data }
    } = await $axios.get("embryo-storage/list-by-patient-id", {
        params: { patientId }
    });
    return data;
}

export async function getFertilizationLineByCycle(cycleId) {
    const {
        data: { fertilizationLines }
    } = await $axios.get("fertilization/fertilization-lines-by-cycle", {
        params: { cycleId }
    });
    return fertilizationLines;
}

export async function saveFreezingLine(payload) {
    const { data } = await $axios.post(
        "embryo-storage/save-freezing-line",
        payload
    );
    return data;
}

export async function editFreezingLine(payload) {
    const { data } = await $axios.post(
        "embryo-storage/edit-freezing-line",
        payload
    );
    return data;
}

export async function deleteFreezingLine(payload) {
    const { data } = await $axios.post(
        "embryo-storage/delete-freezing-line",
        payload
    );
    return data;
}

export async function getCryoStorageOptions() {
    const {
        data: { pugColorList }
    } = await $axios.get("semen-cryo-storage/plug-color-list");
    const {
        data: { cassetteTypeList }
    } = await $axios.get("semen-cryo-storage/cassette-type-list");
    const {
        data: { cassetteColorList }
    } = await $axios.get("semen-cryo-storage/cassette-color-list");

    const {
        data: { dewarList }
    } = await $axios.get("embryo-storage/get-dewar-list");

    return {
        pugColorList,
        cassetteTypeList,
        cassetteColorList,
        dewarList
    };
}

export async function getCryoStorageListFiltered(queryParams) {
    const {
        data: { embryoStorageList }
    } = await $axios.get("embryo-storage/list-filter", {
        params: queryParams
    });
    return embryoStorageList;
}
