import * as artCycleService from "@/service/art-cycle";
import { emitter } from "@/event-bus";
import format from "date-fns/format";
import { DATE_FORMAT_STRING } from "@/config";

const CYCYLE_SUMMARY_KEY = "CYCYLE_SUMMARY";
const PATIENT_KEY = "PATIENT";
const PARTNER_KEY = "PARTNER";
const SELECTED_CYCLE_KEY = "SELECTED_CYCLE";
const CYCLE_TYPE_OPTIONS_KEY = "CYCLE_TYPE_OPTIONS";

const LAB_DATE = {
    date1: null,
    date2: null,
    date3: null,
    date4: null,
    date5: null,
    date6: null
};

const PATIENT = {
    chart: null,
    age: null,
    firstName: null,
    lastName: null,
    id: null,
    year: null,
    month: null,
    day: null,
    hin: null,
    hin_ver: null,
    sex: null,
    phone: null,
    doctor: null,
    doctorId: null,
    ref_num: null,
    lab_test: [],
    consults: [],
    lab_date: LAB_DATE,
    hepatitisBPos: false,
    hepatitisCPos: false,
    hivPos: false
};

let EMPTY_CYCLE = {
    patientId: null,
    patientName: null,
    cycleId: null,
    cycleNo: null,
    donnations: null,
    no_fetus: null,
    gestationalSac: null,
    heartBeat: null,
    lossFetus: null,
    cycleType: null,
    cycleNoType: null,
    spermSource: null,
    eggSource: null,
    cycleDate: null,
    cycleSuccess: false,
    cycleCompleted: false,
    // simulatedCycle: false,
    eggDoner: false,
    ohip: false,
    surrogate: false,
    others: null,
    cycleCompleteDate: null,
    cancel: false,
    isBornCycle: false,
    bornCycleId: null,
    parentCycleType: null,
    childCycleType: null,
    cycleNumber: null,
    cycleName: null,
    pNumber: null
};

const state = () => {
    const patient = localStorage.getItem(PATIENT_KEY)
        ? JSON.parse(localStorage.getItem(PATIENT_KEY))
        : PATIENT;

    const partner = localStorage.getItem(PARTNER_KEY)
        ? JSON.parse(localStorage.getItem(PARTNER_KEY))
        : PATIENT;

    const cyclesSummary = localStorage.getItem(CYCYLE_SUMMARY_KEY)
        ? JSON.parse(localStorage.getItem(CYCYLE_SUMMARY_KEY))
        : [];

    const selectedCycle = localStorage.getItem(SELECTED_CYCLE_KEY)
        ? JSON.parse(localStorage.getItem(SELECTED_CYCLE_KEY))
        : null;

    const cycleTypeOptions = localStorage.getItem(CYCLE_TYPE_OPTIONS_KEY)
        ? JSON.parse(localStorage.getItem(CYCLE_TYPE_OPTIONS_KEY))
        : null;

    const globalScannedBarcode = null;
    return {
        cyclesSummary: cyclesSummary,
        patient: patient,
        partner: partner,
        cycleTypes: null,
        selectedCycle: selectedCycle,
        selectedCycleNo: null,
        cycleTypeOptions: [],
        spermSourceOptions: null,
        eggSourceOptions: null,
        globalScannedBarcode: null
    };
};

const mutations = {
    SET_SELECTED_CYCLE_CANCELLED(state, isCancelled) {
        if (state.selectedCycle) {
            state.selectedCycle.cancel = isCancelled;
            const cycle = state.cyclesSummary.find(item => item.cycleId === state.selectedCycle.cycleId);
            if (cycle) {
                cycle.cancel = isCancelled;
                if (isCancelled) {
                    cycle.cycleCompleted = false;
                }
            }


            console.log(state.cyclesSummary);
        }
    },
    SET_SELECTED_FETUSHIST(state, festusItem) {
        if (state.selectedCycle) {
            const cycle = state.cyclesSummary.find(item => item.cycleId === state.selectedCycle.cycleId);
            if (cycle) {
                if(festusItem) {
                    //cycle.gestationalSac = festusItem.gestationalSac;
                    //cycle.heartBeat = festusItem.heartBeat;
                    cycle.lossFetus = festusItem.heartBeat > 0 ? 0 : 1;
                } else {
                    //cycle.gestationalSac = null;
                    //cycle.heartBeat = null;
                    cycle.lossFetus = null;
                }
            }
        }
    },
    SET_SELECTED_CYCLE(state, payload) {
        state.selectedCycle = payload;
        localStorage.setItem(
            SELECTED_CYCLE_KEY,
            JSON.stringify({ ...payload })
        );
    },
    SET_SELECTED_CYCLE_TO_FIRST(state) {
        let index = state.cyclesSummary.findIndex(
            (item) => item.cycleNo == state.cyclesSummary.length
        );
        state.selectedCycle = state.cyclesSummary[index];
    },
    SET_BORN_CYCLE(state, { cycleId, bornCycleId }) {
        let index = state.cyclesSummary.findIndex(
            (item) => item.cycleId == cycleId
        );
        state.cyclesSummary[index].bornCycleId = bornCycleId;
    },
    SET_DONATIONS_CYCLE(state, { cycleId, numOfDonations }) {
        let index = state.cyclesSummary.findIndex(
            (item) => item.cycleId == cycleId
        );
        state.cyclesSummary[index].numOfDonations = numOfDonations;
    },
    SET_CYCLE_DETAILS(
        state,
        { petientDetails, partnerDetails, cyclesSummery, cycleTypes, selectedCycleId }
    ) {
        state.cyclesSummary = cyclesSummery.map((item) => ({
            ...item,
            eggDonor: item.eggDoner
        }));
        state.patient = petientDetails ? petientDetails : { ...PATIENT };
        state.partner = partnerDetails ? partnerDetails : { ...PATIENT };

        localStorage.setItem(
            PATIENT_KEY,
            petientDetails
                ? JSON.stringify({ ...state.patient })
                : JSON.stringify(PATIENT)
        );
        localStorage.setItem(
            PARTNER_KEY,
            partnerDetails
                ? JSON.stringify({ ...state.partner })
                : JSON.stringify(PATIENT)
        );

        localStorage.setItem(
            CYCYLE_SUMMARY_KEY,
            JSON.stringify([...state.cyclesSummary])
        );
        localStorage.setItem(
            CYCLE_TYPE_OPTIONS_KEY,
            JSON.stringify([...state.cycleTypeOptions])
        );

        if (cyclesSummery.length > 0) {
            console.log(selectedCycleId);

            const selectedCycleSummery = cyclesSummery.find(row => row.cycleId.toString() === selectedCycleId);

            console.log(selectedCycleSummery);
            // Check if an object was found
            if (selectedCycleSummery) {
                state.selectedCycle = selectedCycleSummery;
                localStorage.setItem(
                    SELECTED_CYCLE_KEY,
                    JSON.stringify(selectedCycleSummery)
                );
            } else {
                state.selectedCycle = cyclesSummery[0];
                localStorage.setItem(
                    SELECTED_CYCLE_KEY,
                    JSON.stringify(cyclesSummery[0])
                );
            }


        } else {
            state.selectedCycle = { ...EMPTY_CYCLE };
            localStorage.setItem(
                SELECTED_CYCLE_KEY,
                JSON.stringify({ ...EMPTY_CYCLE })
            );
        }
    },
    SET_UPDATE_LABTEST_ONSTORAGE(state, { patientId, labTest, labDate }) {
        if (state.patient != null && state.patient.chart == patientId) {
            state.patient.lab_test = labTest;
            state.patient.lab_date = labDate;

            localStorage.setItem(
                PATIENT_KEY,
                JSON.stringify({ ...state.patient })
            );
        }

        if (state.partner != null && state.partner.chart == patientId) {
            state.partner.lab_test = labTest;
            state.partner.lab_date = labDate;

            localStorage.setItem(
                PARTNER_KEY,
                JSON.stringify({ ...state.partner })
            );
        }
    },
    CLEAR_PATIENT_AND_CYCLE_DATA(state) {
        state.patient = { ...PATIENT };
        state.partner = { ...PATIENT };
        state.cyclesSummary = [];
        state.selectedCycle = { ...EMPTY_CYCLE };
    },

    setHepatitisBPos(state, value) {
        state.patient.hepatitisBPos = value;
    },
    setHepatitisCPos(state, value) {
        state.patient.hepatitisCPos = value;
    },
    setHivPos(state, value) {
        state.patient.hivPos = value;
    },
    setConsults(state, value) {
        state.patient.consults = value;
    },
};

const getters = {
    getSelectedCycle: ({ cyclesSummary, selectedCycleNo }) => {
        let index = cyclesSummary.findIndex(
            (item) => item.cycleNo === selectedCycleNo
        );
        if (index != -1) {
            return cyclesSummary[index];
        } else {
            return null;
        }
    },
    isBornCycle({ selectedCycle }) {
        if (selectedCycle) {
            return !!selectedCycle.bornCycleId;
        } else {
            return false;
        }
    },
    isCreateBornCycle({ selectedCycle }) {
        if (selectedCycle) {
            return (
                selectedCycle.isBornCycle && selectedCycle.bornCycleId == null
            );
        } else {
            return false;
        }
    },
    getSelectedCycleAndPatientId(state) {
        if (state.selectedCycle) {
            return {
                patientId: state.patient.id,
                cycleId: state.selectedCycle.cycleId
                    ? state.selectedCycle.cycleId
                    : 0
            };
        } else {
            return {
                patientId: null,
                cycleId: null
            };
        }
    },
    isIVFCycle({ selectedCycle }) {
        return selectedCycle ?.cycleType === "IVF";
    },
    isFETCycle({ selectedCycle }) {
        return selectedCycle ?.cycleType === "FET";
    },
    isCompletedCycle({ selectedCycle }) {
        return selectedCycle ?.cycleCompleted;
    },
    latestCycleId({ cyclesSummary }) {
        if (cyclesSummary.length == 0) {
            return 0;
        }
        return cyclesSummary[0] ?.cycleNo;
    }
};

const actions = {
    updateGLobalScannedBarcode({ state }, payload) {
        state.globalScannedBarcode = payload;
    },
    async getCyclesDetails({ commit }, demographicNo) {
        try {
            const {
                petientDetails,
                partnerDetails,
                cyclesSummery,
                cycleTypes
            } = await artCycleService.getCycleSummaryDetail(demographicNo);

            const selectedCycleId = null;

            commit("SET_CYCLE_DETAILS", {
                petientDetails,
                partnerDetails,
                cyclesSummery,
                cycleTypes,
                selectedCycleId
            });

            return Promise.resolve();
        } catch (error) {
            return Promise.reject();
        }
    },
    async getSelectedCyclesDetails({ commit }, { demographicNo, cycelId }) {
        try {
            const {
                petientDetails,
                partnerDetails,
                cyclesSummery,
                cycleTypes
            } = await artCycleService.getCycleSummaryDetail(demographicNo);

            commit("SET_CYCLE_DETAILS", {
                petientDetails,
                partnerDetails,
                cyclesSummery,
                cycleTypes,
                selectedCycleId: cycelId
            });

            return Promise.resolve();
        } catch (error) {
            return Promise.reject();
        }
    },
    updateSelectedCycle({ state }, cycle) {
        state.selectedCycle = cycle;
    },
    async updateCycle({ state }, cycle) {
        try {
            const data = await artCycleService.updateCycle(cycle);
            return Promise.resolve();
        } catch (err) {
            return Promise.reject(err);
        }
    },
    nextCycle({ state, commit }) {
        let index = state.cyclesSummary.findIndex(
            (item) => item.cycleNo === state.selectedCycle.cycleNo + 1
        );
        if (index != -1) {
            commit("SET_SELECTED_CYCLE", state.cyclesSummary[index]);
        }
    },
    previsouCycle({ state, commit }) {
        let index = state.cyclesSummary.findIndex(
            (item) => item.cycleNo === state.selectedCycle.cycleNo - 1
        );
        if (index != -1) {
            commit("SET_SELECTED_CYCLE", state.cyclesSummary[index]);
        }
    },
    async getCycleFormFieldOptions({ state }) {
        const {
            spermSources,
            eggSource,
            cycleTypes
        } = await artCycleService.getFormFieldOptions();

        state.cycleTypeOptions = cycleTypes;
        state.spermSourceOptions = spermSources;
        state.eggSourceOptions = eggSource;
    },
    async createNewCycle({ state, dispatch }, payload) {
        try {
            const { data } = await artCycleService.createNewCycle(payload);
            return dispatch("getCyclesDetails", state.patient.id);
        } catch (err) {
            return Promise.reject();
        }
    },
    async deleteCycle({ state, dispatch }, cycelId) {
        try {
            const {
                data: { data }
            } = await artCycleService.deleteCycle(cycelId);

            if (data) {
                let index = state.cyclesSummary.findIndex(
                    (item) => item.cycleId === data.cycleId
                );
                state.cyclesSummary.splice(index);
                return dispatch("getCyclesDetails", state.patient.id);
            }
        } catch (error) {
            return Promise.reject();
        }
    },
    async createNewBornCycle({ commit }, born) {
        try {
            const data = await artCycleService.craetNewBorn(born);
            return Promise.resolve(data);
        } catch (err) {
            return Promise.reject();
        }
    },
    async mergeCycle({ state }, payload) {
        try {
            const data = await artCycleService.mergeCycle(payload);
            return data;
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async getCouplesReport({ state }) {
        try {
            const { reslult } = await artCycleService.getCouplesReport(
                state.patient.id
            );
            return reslult;
        } catch (err) {
            return Promise.reject(err);
        }
    }
};

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
};
