import { $axios } from "./app-http";

export async function getUsers() {
    const {
        data: { apiUsers }
    } = await $axios.get("api-user-v2/list");
    return apiUsers;
}

export async function createUser(payload) {
    const { data } = await $axios.post("api-user-v2/create", payload);
    return data;
}

export async function deleteUser(id) {
    const { data } = await $axios.post(`api-user/delete/${id}`);
    return data;
}

export async function updateUser(id, payload) {
    const { data } = await $axios.post(`api-user-v2/update/${id}`, payload);
    return data;
}

export async function updateUserStatus(id, payload) {
    const { data } = await $axios.post(
        `api-user/update-user-status/${id}`,
        payload
    );
    return data;
}

export async function updatePassword(payload) {
    return await $axios.post("api-user/change-password", payload);
}

export async function updatePasswordAdmin(payload) {
    return await $axios.post("api-user-v2/update-user-password", payload);
}

export async function getUserGroups() {
    const { data } = await $axios.get("api-groups/list");
    return data;
}
