import { $axios } from "./app-http";

export async function getEmbryoTranasfer(cycleId) {
    const {
        data: { embryoTransfer }
    } = await $axios.get("embryo-transfer/get-embryo-transfer", {
        params: { cycleId }
    });

    return embryoTransfer;
}

export async function updateEmbryoTrasfer(payload) {
    const { data } = await $axios.post("embryo-transfer/save-embryo-transfer", {
        ...payload
    });
    return data;
}

export async function createEmbryoTransfer(payload) {
    const { data } = await $axios.post(
        "embryo-transfer/create-embryo-transfer",
        { ...payload }
    );
    return data;
}

export async function getDaysPostORList() {
    const {
        data: { daysPostORList }
    } = await $axios.get("embryo-transfer/get-days-post-or-list");

    return daysPostORList;
}

export async function getCatheterList() {
    const {
        data: { catheterList }
    } = await $axios.get("embryo-transfer/get-catheter-list");
    return catheterList;
}

export async function deleteEmbryoTranfer(cycleId) {
    const { data } = await $axios.post(
        "embryo-transfer/delete-embryo-transfer",
        { cycleId }
    );
    return data;
}

export async function getTransferredEmbryos(cycleId) {
    const { data } = await $axios.get(
        "fertilization/transfer-fertilization-lines",
        {
            params: { cycleId }
        }
    );
    return data;
}
