import {
    getBloodGroupTests,
    getBloodTestResults,
    addBloodTestOrderItems,
    getBloodTest,
    addBloodTest,
    saveAllBloodTest,
    deleteOrder,
    deleteResult,
    cancelBloodTest,
    printBarcodePDF
} from "@/service/blood-test";

const state = () => ({
    bloodGroups: []
});

const actions = {
    async getBloodTestResults({ rootState }) {
        try {
            const data = getBloodTestResults(rootState.artCycle.patient.id);
            return data;
        } catch (err) {
            return Promise.reject();
        }
    },
    async getBloodGroupTests({ state }) {
        try {
            if (state.bloodGroups.length == 0) {
                state.bloodGroups = await getBloodGroupTests();
            }
            return Promise.resolve();
        } catch (err) {
            return Promise.reject();
        }
    },
    async addBloodTestOrderItems({ state }, reqBody) {
        try {
            const data = await addBloodTestOrderItems(reqBody);
            return data;
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async getBloodTest({ state }, testOrderId) {
        try {
            const data = await getBloodTest(testOrderId);
            return data;
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async addBloodTest({ dispatch }, payload) {
        try {
            const data = await addBloodTest(payload);
            return data;
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async saveAllBloodTest({ dispatch }, payload) {
        try {
            const data = await saveAllBloodTest(payload);
            return data;
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async printBarcodePDF({ state }, id) {
        try {
            const data = await printBarcodePDF(id);
            return data;
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async deleteResult({ state }, testOrderId) {
        try {
            const data = await deleteResult(testOrderId);
            return Promise.resolve();
        } catch (err) {
            return Promise.reject();
        }
    },
    async deleteOrder({ state }, testOrderId) {
        try {
            const data = await deleteOrder(testOrderId);
            return Promise.resolve();
        } catch (err) {
            return Promise.reject();
        }
    },
    async cancelBloodTest({ state }, { testOrderId, testCode }) {
        try {
            const data = await cancelBloodTest(testOrderId, testCode);
            return Promise.resolve();
        } catch (err) {
            return Promise.resolve();
        }
    }
};

const getters = {
    getBloodGroups(state) {
        return [
            ...state.bloodGroups.map((item) => ({
                groupName: item.groupName,
                curGroup: item.curGroup
            }))
        ];
    },
    getAllBloodTest({ bloodGroups }) {
        const booodTests = [];
        for (const group of bloodGroups) {
            booodTests.push(...group.testItems);
        }
        return [
            ...new Map(
                booodTests.map((item) => [item["testId"], item])
            ).values()
        ];
    }
};

export default {
    namespaced: true,
    state,
    actions,
    getters
};
