import * as bornService from "@/service/born";

const state = () => ({
    complications: null,
    obstetricalHistory: null,
    ovarian: null,
    oocyteCollections: null,
    pregnancyOutcome: null,
    freshEmbryos: null,
    frozenEmbryos: null,
    embryoTransfer: null,
    isLoaded: false,
    primaryCycleInfo: false,
    dependentOn: {
        embryoTransfer: null,
        noOfBabiesBorn: null,
        oocytesInseminatedInjected: null,
        oocytesRetrieved: null,
        reasonForTreatmentCycle: null,
        oocytesCryopreserved: null,
        cycleCancelled: null,
        pgtAPlanned: null,
        oocyteCollectionDate: null,
        sourceTransferredEmbryo: null,
        utilizableEmbryos: null,
        embryosCryopreserved: null,
        utilizableAfterThaw: null,
        embryosThawed: null
    },
    fertilizationEmbryoThawDate: null
});

const actions = {
    async getBorn({ commit }, { patientId, cycleId }) {
        try {
            const data = await bornService.getBorn({ patientId, cycleId });
            commit("SET_BORN", data);
            return Promise.resolve();
        } catch (err) {
            commit("SET_EMPTY_BORN");
        }
    },
    async getBronV2(
        { state, commit },
        { patientId, cycleId, isBornCycle, bornCycleId }
    ) {
        if (!isBornCycle) {
            return;
        }
        try {
            const {
                complications,
                obstetricalHistory,
                oocyteCollections,
                pregnancyOutcome,
                freshEmbryos,
                frozenEmbryos,
                embryoTransfer,
                ovarian,
                primaryCycleInfo
            } = await bornService.getBorn({ patientId, cycleId });

            
            state.complications = complications;
            state.obstetricalHistory = obstetricalHistory;
            state.oocyteCollections = oocyteCollections;
            state.pregnancyOutcome = pregnancyOutcome;
            state.freshEmbryos = freshEmbryos;
            state.frozenEmbryos = frozenEmbryos;
            state.embryoTransfer = embryoTransfer;
            state.ovarian = ovarian;
            state.primaryCycleInfo = primaryCycleInfo;

            state.isLoaded = true;

            commit(
                "SET_PGT_A_PLANNED",
                obstetricalHistory.pgtAPlanned
                    ? obstetricalHistory.pgtAPlanned
                    : "N"
            );

            return Promise.resolve();
        } catch (err) {
            return Promise.reject();
        }
    },
    async updateComplication({ commit }, payload) {
        try {
            const data = await bornService.updateComplication(payload);
            return data;
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async updatePregnancyOutcome({ state }, payload) {
        try {
            const data = await bornService.updatePregnancyOutcome(payload);
            return data;
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async updateOocyteCollection({ commit }, payload) {
        try {
            const data = await bornService.updateOocyteCollection(payload);
            return data;
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async updateObstetricalHistory({ commit }, payload) {
        try {
            const data = await bornService.updateObstetricalHistory(payload);
            return data;
        } catch (err) {
            return Promise.reject();
        }
    },
    async updateEmbryology({ commit }, payload) {
        try {
            const data = await bornService.updateEmbryology(payload);
            return data;
        } catch (err) {
            return Promise.reject(data);
        }
    },
    async updateFreshEmbryo({ commit }, payload) {
        try {
            const data = await bornService.updateFreshEmbryo(payload);
            return data;
        } catch (err) {
            Promise.reject(err);
        }
    },
    async updateFrozenEmbryo({ commit }, payload) {
        try {
            const data = await bornService.updateFrozenEmbryo(payload);
            return data;
        } catch (err) {
            Promise.reject(err);
        }
    },
    async updateEmbryoTransfer({ commit }, payload) {
        try {
            const data = await bornService.updateEmbryoTransfer(payload);
            return data;
        } catch (err) {
            Promise.reject(err);
        }
    },
    async updateObstetricalHistory({ commit }, payload) {
        try {
            const data = await bornService.updateObstetricalHistoryV2(payload);
            return data;
        } catch (err) {
            Promise.reject(err);
        }
    },
    async updateOvarian({ commit }, payload) {
        try {
            const data = await bornService.updateOvarian(payload);
            return data;
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async saveBornExtra({ commit }, payload) {
        try {
            const data = await bornService.saveBornExtra(payload);
            return data;
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async getBornExtra({ commit }, { cycleId, patientId }) {
        try {
            const { bornExtraData } = await bornService.getBornExtra(
                cycleId,
                patientId
            );
            return bornExtraData;
        } catch (err) {
            return Promise.reject();
        }
    },
    async updateAllBornPart({ commit }, payload) {
        try {
            const data = await bornService.updateAllBornPart(payload);
            return data;
        } catch (err) {
            return Promise.reject();
        }
    }
};

const mutations = {
    SET_BORN(
        state,
        { complications, obstetricalHistory, oocyteCollections, embryology }
    ) {
        state.complications = complications;
        state.obstetricalHistory = obstetricalHistory;
        state.oocyteCollections = oocyteCollections;
        state.embryology = embryology;
    },
    SET_EMPTY_BORN(state) {
        state.complications = null;
        state.obstetricalHistory = null;
        state.oocyteCollections = null;
        state.pregnancyOutcome = null;
        state.freshEmbryos = null;
        state.frozenEmbryos = null;
        state.embryoTransfer = null;
    },
    SET_NO_BABIES_BORN(state, value) {
        state.dependentOn.noOfBabiesBorn = value;
    },
    SET_OOCYTES_INSEMINATED_INJECTED(state, value) {
        state.dependentOn.oocytesInseminatedInjected = value;
    },
    SET_EMBRYO_TRANSFER(state, value) {
        state.dependentOn.embryoTransfer = value;
    },
    SET_OOCYTES_RETRIEVED(state, value) {
        state.dependentOn.oocytesRetrieved = value;
    },
    SET_OOCYTE_CRYO_PRESERVED(state, value) {
        state.dependentOn.oocytesCryopreserved = value;
    },
    SET_REASON_FOR_TREATMENT_CYCLE(state, value) {
        state.dependentOn.reasonForTreatmentCycle = value;
    },
    SET_CYCLE_CANCELLED(state, value) {
        state.dependentOn.cycleCancelled = value;
    },
    SET_PGT_A_PLANNED(state, value) {
        state.dependentOn.pgtAPlanned = value;
    },
    SET_OOCYTE_COLLECTION_DATE(state, value) {
        state.dependentOn.oocyteCollectionDate = value;
    },
    SET_UTILIZABLE_EMBRYOS(state, value) {
        state.dependentOn.utilizableEmbryos = value;
    },
    SET_EMBRYOS_CRYOPRESERVED(state, value) {
        state.dependentOn.embryosCryopreserved = value;
    },
    SET_UTILIZABLE_AFTER_THAW(state, value) {
        state.dependentOn.utilizableAfterThaw = value;
    },
    SET_EMBRYOS_THAWED(state, value) {
        state.dependentOn.embryosThawed = value;
    },
    SET_FERTILIZATION_EMBRYO_THAW_DATE(state, value) {
        state.fertilizationEmbryoThawDate = value;
    }
};

const getters = {};

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
};
