import * as cycleReportService from "@/service/cycle-report";

const state = () => ({
    cycleTypes: null,
    reportType: "cycle",
    report: [],
    gData: [],
    form: {
        reportType: state.reportType,
        cycleTypes: [],
        funded: null,
        from: null,
        to: null,
        cancelled: null,
        spermDonor: null,
        siteId: null,
        cycleDoctor: [],
        demographicDoctor: [],
        comments: null
    },
    form2: {
        cycleTypes: [],
        date: null,
        siteId: null,
    },

    locationOptions: [
        { id: -1, name: "All" },
        { id: 0, name: "None" }
    ]
});

const actions = {
    updateReportType({ state }, payload) {
        state.reportType = payload;
    },
    emptyTheReport({ state }) {
        state.report = [];
    },
    async getAllCycleTypes({ state }) {
        try {
            const cycleTypes = await cycleReportService.getAllCycleType();
            state.cycleTypes = cycleTypes;
        } catch (err) {
            console.log(err);
        }
    },
    async getCycleReport({ state }, payload) {
        try {
            const data = await cycleReportService.getCycleRport(payload);
            state.report = data;
            return Promise.resolve();
        } catch (err) {
            state.report = [];
            return Promise.resolve();
        }
    },
    async getCycleKPIC({ state }, payload) {
        try {
            const data = await cycleReportService.getCycleKPIC(payload);
            state.gData = data;
            return Promise.resolve();
        } catch (err) {
            state.gData = [];
            return Promise.resolve();
        }
    },
    async getDailyCycleReport({ state }, payload) {
        try {
            const data = await cycleReportService.getDailyCycleRport(payload);
            state.report = data;
            return Promise.resolve();
        } catch (err) {
            state.report = [];
            return Promise.resolve();
        }
    },
    async getLocationOption({ state }) {
        try {
            if (state.locationOptions.lenght <= 2) {
                const data = await cycleReportService.getLocationOption();
                state.locationOptions.push(...data);
            }
        } catch (err) {}
    },
    async getCycleReportExcel({ state }, payload) {
        try {
            const data = await cycleReportService.getCycleReportExcel(payload);
            return data;
        } catch (err) {
            return Promise.reject();
        }
    },

    async getDailyCycleReportExcel({ state }, payload) {
        try {
            const data = await cycleReportService.getDailyCycleReportExcel(payload);
            return data;
        } catch (err) {
            return Promise.reject();
        }
    }
};

const mutations = {};
const getters = {
    getForm(state) {
        return state.form;
    },
    getForm2(state) {
        return state.form2;
    },
};

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
};
