import * as monitoringService from "@/service/cycle-monitoring";

const state = () => ({
    diagnosisOptions: null,
    protocolOptions: null,
    drugs1Options: null,
    drugs2Options: null,
    details: null,
    lines: [],
    patientVisits: null,
    comments: null,
    selectedMonitorLine: null,
    bloodTests: {
        fsh: null,
        tsh: null,
        prl: null,
        bhcg: null,
        testo: null,
        dheas: null,
        amh: null
    },
    monitorDetails: {
        //no_fetus: null,
        gestationalSac: null,
        heartBeat: null,
        lossFetus: null
    },
    selectedComment: null,
    inventoryList: []
});

const mutations = {
    SET_SELECTED_MONITOR_LINE(state, payload) {
        state.selectedMonitorLine = payload;
    },
    SET_SELECTED_COMMMENT(state, payload) {
        state.selectedComment = payload;
    }
};

const actions = {
    async getCycleMonitoringDetails({ state }, cycleId) {
        try {
            const {
                monitorDetails,
                monitorLines,
                monitorLineComments,
                bloodTests,
                patientVisits
            } = await monitoringService.getCycleMonitoringDetails(cycleId);

            state.details = monitorDetails;
            state.monitorDetails = monitorDetails;
            state.lines = monitorLines;
            state.comments = monitorLineComments;
            state.patientVisits = patientVisits;
            state.bloodTests = bloodTests;

            if (monitorDetails) {
                Promise.resolve();
            }
        } catch (error) {
            Promise.reject();
        }
    },
    async getFormFieldOptions({ state }) {
        if (!state.diagnosisOptions) {
            const diagnosis = await monitoringService.getDaignosisOptions();
            state.diagnosisOptions = diagnosis;
        }

        if (!state.protocolOptions) {
            const protocols = await monitoringService.getProtocolOptions();
            state.protocolOptions = protocols;
        }

        if (!state.drugs1Options) {
            const drugs1 = await monitoringService.getDrug1Options();
            state.drugs1Options = drugs1;
        }

        if (!state.drugs2Options) {
            const drugs2 = await monitoringService.getDrug2Options();
            state.drugs2Options = drugs2;
        }
    },
    async createMonitorLine({ state }, formData) {
        try {
            const data = await monitoringService.createMonitorLine({
                ...formData,
                curUser: "lena"
            });
            return Promise.resolve();
        } catch (error) {
            return Promise.reject(error.response.data);
        }
    },
    async editMonitorLine({ state }, formData) {
        try {
            const data = await monitoringService.updateMonitorLine({
                ...formData,
                curUser: "lena"
            });
            return Promise.resolve();
        } catch (error) {
            return Promise.reject(error.response.data);
        }
    },
    async updateComment({ state, dispatch }) {
        try {
            const { data } = await monitoringService.updateComment({
                ...state.selectedComment,
                curUser: "lena"
            });

            await dispatch("dailyReport/saveCheckDefault", data.testOrderId, {
                root: true
            });

            return Promise.resolve();
        } catch (error) {
            return Promise.reject();
        }
    },
    async saveMonitoringSummary({ state }, formData) {
        try {
            const { data } = await monitoringService.saveMonitoringSummary(
                formData
            );
            return data;
        } catch (error) {
            console.log(error);
        }
    },
    async deleteCycleMonitorLine({ dispatch, state, rootState }) {
        try {
            const data = await monitoringService.deleteCycleMonitorLine(
                state.selectedMonitorLine.cm_lines_id,
                rootState.artCycle.patient.id,
                rootState.user.user
            );
            return dispatch(
                "getCycleMonitoringDetails",
                rootState.artCycle.selectedCycle.cycleId
            );
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async getMonitorLineDeleteLog({ state }, params) {
        try {
            const data = await monitoringService.getMonitorLineDeleteLog(
                params.fromDate,
                params.toDate,
                params.patient
            );
            return data;
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async restoreMonitorLine({ dispatch }, cmLineId) {
        try {
            const data = await monitoringService.restoreMonitorLine(cmLineId);
            return data;
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async getInventoryList({ state }) {
        try {
            const {
                medicationInventoryList
            } = await monitoringService.getInventoryList();
            state.inventoryList = medicationInventoryList;
            return state.inventoryList;
        } catch (error) {
            return Promise.reject(error);
        }
    },
    async saveInvetory({ dispatch }, payload) {
        try {
            const data = await monitoringService.saveInvetory(payload);
            dispatch("getInventoryList");
            return Promise.resolve();
        } catch (err) {
            return Promise.reject();
        }
    },
    async updateInvetory({ dispatch }, payload) {
        try {
            const data = await monitoringService.updateInvetory(payload);
            dispatch("getInventoryList");
            return Promise.resolve();
        } catch (err) {
            return Promise.reject();
        }
    },
    async deleteInventory({ dispatch }, payload) {
        try {
            const data = await monitoringService.deleteInventory(payload);
            dispatch("getInventoryList");
            return Promise.resolve();
        } catch (err) {
            return Promise.reject();
        }
    }
};

const getters = {
    latestLine({ lines }) {
        return lines[lines.length - 1];
    },
    lastValidLH({ lines }) {
        console.log(lines);
        return 2;
    }
};

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
};
