import * as semenService from "@/service/semen";

const state = () => ({
    semenList: null,
    selectedSemen: null
});

const actions = {
    async getSemenList({ commit, getters }, patient) {
        try {
            const { semenList } = await semenService.getSemenList(patient);
            commit("SET_SEMEN_LIST", semenList);
            //commit("SET_SELECTED_SEMEN", getters["getLastSemen"]);
            return Promise.resolve();
        } catch (error) {
            commit("SET_SEMEN_LIST", []);
            return Promise.reject(error.response.data);  
        }
    },
    async updateSemen({ state }, semen) {
        try {
            const data = await semenService.updateSemen(semen);
            return Promise.resolve();
        } catch (error) {
            return Promise.reject();
        }
    },
    async createSemen({ state }, semen) {
        try {
            const data = await semenService.createSemen(semen);
            return Promise.resolve();
        } catch (error) {
            return Promise.reject(error);
        }
    },
    async deleteSemen({ state }, semen) {
        try {
            const data = await semenService.delteSemen(semen);
            return Promise.resolve();
        } catch (error) {
            return Promise.reject(error);
        }
    }
};

const mutations = {
    SET_SEMEN_LIST(state, list) {
        state.semenList = list;
    },
    SET_SELECTED_SEMEN(state, semen) {
        state.selectedSemen = semen;
    }
};

const getters = {
    getPatient: (state, getters, rootState) => {

        let person = rootState.artCycle.patient && rootState.artCycle.patient.sex === 'M' ? rootState.artCycle.patient : 
                            (rootState.artCycle.partner && rootState.artCycle.partner.sex === 'M' ? rootState.artCycle.partner : rootState.artCycle.patient);
        return person
            ? {
                  id: person.id,
                  name: `${person.firstName} ${person.lastName}`,
                  dateOfBirth: `${person.year}-${person.month}-${person.day}`
              }
            : { id: null, name: null, dateOfBirth: null };
    },
    getPartner: (state, getters, rootState) => {
        
        let person = rootState.artCycle.patient && rootState.artCycle.patient.sex === 'M' ? rootState.artCycle.partner : 
                            (rootState.artCycle.partner && rootState.artCycle.partner.sex === 'M' ? rootState.artCycle.patient : rootState.artCycle.partner);
        return person
            ? {
                  id: person.id,
                  name: `${person.firstName} ${person.lastName}`,
                  dateOfBirth: `${person.year}-${person.month}-${person.day}`
              }
            : { id: null, name: null, dateOfBirth: null };
    },
    getLastSemen: (state) => {
        return state.semenList.at(-1);
    }
};

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
};
