import { DATE_FORMAT_STRING } from "@/config";
import * as fertilizationService from "@/service/fertilization";
import { format } from "date-fns";

const state = () => ({
    fertilization: null,
    fertilizationLines: [],
    maturityList: [],
    stageList: [],
    gradeList: [],
    mediaInventoryList: [],
    primaryCycles: [],
    cycleTypes: [],
    freezerEquipments: [],
    freezerItems: []
});

const actions = {
    async getFertilization({ commit, state }, cycleId) {
        try {
            const data = await fertilizationService.getFertilizationByCycleId(
                cycleId
            );
            commit("SET_FERTILIZATION", data);
            return Promise.resolve(data);
        } catch (err) {
            state.fertilizationLines = [];
            return Promise.reject();
        }
    },
    async createFertlization({ state }, payload) {
        try {
            const data = await fertilizationService.createFertlization(payload);
            return Promise.resolve();
        } catch (err) {
            return Promise.reject();
        }
    },
    async updateFertlization({ dispatch, state }, payload) {
        try {
            const data = await fertilizationService.updateFertlization(payload);
            return data;
        } catch (err) {
            return Promise.reject();
        }
    },
    async getFertilizationLines({ state }, fertId) {
        try {
            const data = await fertilizationService.getFertilizationLine(
                fertId
            );
            state.fertilizationLines = data;
            return Promise.resolve();
        } catch (err) {
            state.fertilizationLines = [];
            return Promise.reject();
        }
    },
    async createFertilizationLine({ state, dispatch }, payload) {
        try {
            const data = await fertilizationService.createFertilizationLine(
                payload
            );
            await dispatch("getFertilizationLines", state.fertilization.fertId);
            return Promise.resolve();
        } catch (err) {
            return Promise.reject();
        }
    },
    async editFertilizationLines({ state, dispatch }, payload) {
        try {
            const data = await fertilizationService.editFertilizationLine(
                payload
            );
            await dispatch("getFertilizationLines", state.fertilization.fertId);
            return Promise.resolve();
        } catch (err) {
            return Promise.reject();
        }
    },
    async deleteFertilizationLine({ state, dispatch }, payload) {
        try {
            const data = await fertilizationService.deleteFertilizationLine(
                payload
            );
            return dispatch(
                "getFertilizationLines",
                state.fertilization.fertId
            );
        } catch (err) {
            return Promise.reject();
        }
    },
    async deleteFertilizationLines({ state, dispatch }, payload) {
        try {
            const data = await fertilizationService.deleteFertilizationLines(
                payload
            );
            return dispatch(
                "getFertilizationLines",
                state.fertilization.fertId
            );
        } catch (err) {
            return Promise.reject();
        }
    },
    async copyFertilizationLine({ state, dispatch }, howMany) {
        console.log(state.fertilization.fertId);
        try {
            const data = await fertilizationService.copyFertilizationLine(
                state.fertilization.fertId,
                howMany
            );
            return dispatch(
                "getFertilizationLines",
                state.fertilization.fertId
            );
        } catch (err) {
            return Promise.reject();
        }
    },

    async getMediaInventoryOptions({ state }) {
        const { mediaInventoryList } = await fertilizationService.getMediaInventoryList();
        state.mediaInventoryList = mediaInventoryList;        
    },
    
    async getOccyteEmbryoDialogOptions({ state }) {
        try {
            const {
                maturityList,
                stageList,
                gradeList,
                mediaInventoryList
            } = await fertilizationService.getOocyteEmbryoOptions();
            state.maturityList = maturityList;
            state.stageList = stageList;
            state.gradeList = gradeList;
            state.mediaInventoryList = mediaInventoryList;          
        } catch (err) {}
    },
    async saveFertlizationEmbryoDetail({ state, dispatch }, payload) {
        try {
            const data = await fertilizationService.saveFertlizationEmbryoDetail(
                payload
            );
            await dispatch("getFertilizationLines", state.fertilization.fertId);
            return data;
        } catch (err) {
            return Promise.reject(err.response.data);
        }
    },
    async getFertlizationEmbryoDetail({ state }, oocId) {
        try {
            const data = await fertilizationService.getFertlizationEmbryoDetail(
                oocId
            );
            return data;
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async getFertlizationEmbryoDetailFromStart({ state }, { lineId, dayNo }) {
        try {
            const data = await fertilizationService.getFertlizationEmbryoDetailFromStart(
                lineId,
                dayNo
            );
            return data;
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async saveFertilizaionFollAspiration({ state }, payload) {
        try {
            const data = await fertilizationService.saveFertilizaionFollAspiration(
                payload
            );
            return Promise.resolve();
        } catch (err) {
            return Promise.reject();
        }
    },
    async getPrimaryCycles({ state, rootState }) {
        let cycleDate = format(
            new Date(rootState.artCycle.selectedCycle.cycleDate),
            DATE_FORMAT_STRING
        );
        let cycles = await fertilizationService.getPrimaryCycles(
            rootState.artCycle.selectedCycle.patientId,
            cycleDate
        );

        state.primaryCycles = cycles.map((item) => ({
            ...item,
            label: (item.simulated_cycle==0 ? '' : 'SIM ') + `${item.cycle_type} cycle ${item.cycleId} (${item.cycleDate})`
        }));
    },
    async getCycleTypes({ state }) {
        state.cycleTypes = await fertilizationService.getCycleTypes();
    },
    async getAllFreezers({ state }) {
        state.freezerEquipments = await fertilizationService.getAllFreezers();
    },
    async getFreezerItems({ state, rootState }, payload) {
        // console.log(payload);
        const data = await fertilizationService.getFreezerItmes(payload);
        // console.log(data);
        let prevItems = [...state.freezerItems].filter(
            (item) => item.parent != data.parentKey
        );

        if (data.parentKey == null) {
            prevItems = [];
        } else {
            prevItems = [...state.freezerItems].filter(
                (item) => item.parent != data.parentKey
            );
        }

        console.log(data);
        state.freezerItems = [
            ...prevItems,
            ...data.locations.filter((item) => {
                if (item.leaf) {
                    return item.totCapacity > item.totAllocations;
                } else {

                    if(item.properties != undefined && item.properties.patientReff != null && item.properties.patientReff != "") {
                        return (
                            rootState.artCycle.patient.id == item.properties.patientReff
                        );
                    } else {
                        return true;
                    }
                    // if (item.patientReff == null) {
                    //     return true;
                    // } else {
                    //     return (
                    //         rootState.artCycle.patient.id != item.patientReff
                    //     );
                    // }
                }

                // return true;
            })
        ];
    }
};

const mutations = {
    SET_FERTILIZATION(state, payload) {
        state.fertilization = payload;
    },
    EMPTY_FERTILIZAION_LINE(state) {
        state.fertilizationLines = [];
    }
};
const getters = {};

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
};
