import * as opuService from "@/service/opu";
import { DATE_FORMAT_STRING } from "@/config";
import { format } from "date-fns";

const formatToLineObject = (lineStr) => {
    if (!lineStr || lineStr == "") {
        lineStr = "~~|~~|~~|~~|~~|~~|~~";
    }

    return lineStr
        .split("|")
        .map((item) => item.split("~"))
        .map((item, index) => {
            let obj = {};
            obj[`dose${index + 1}`] = item[0];
            obj[`time${index + 1}`] = item[1];
            obj[`initial${index + 1}`] = item[2];
            return obj;
        })
        .reduce((acc, item) => {
            return Object.assign(acc, item);
        }, {});
};

const timeLineFormatter = (lineStr) => {
    if (!lineStr || lineStr == "") {
        lineStr = "~|~|~|~|~|~|~|~|~|~|~|~|~|~|~|~|~";
    }

    const lineLength = lineStr.split("|").length;

    if (lineLength != 17) {
        let addedToLine = "";
        for (let index = 0; index < 17 - lineStr.split("|").length; index++) {
            addedToLine = addedToLine + "~|";
        }
        lineStr = lineStr + addedToLine + "~";
    }

    return lineStr
        .split("|")
        .map((item) => item.split("~"))
        .map((item, index) => {
            let obj = {};
            obj[`timeInit${index + 1}`] = {
                part1: item[0],
                part2: item[1]
            };
            return obj;
        })
        .reduce((acc, item) => {
            return Object.assign(acc, item);
        }, {});
};

const bpLineFormatter = (lineStr) => {
    if (!lineStr || lineStr == "") {
        lineStr = "||||||||||||||||";
    }

    return lineStr
        .split("|")
        .map((item, index) => {
            let obj = {};
            obj[`timeInit${index + 1}`] = item;
            return obj;
        })
        .reduce((acc, item) => {
            return Object.assign(acc, item);
        }, {});
};

const o2LineFormatter = (lineStr) => {
    if (!lineStr || lineStr == "") {
        lineStr = "||||||||||||||||";
    }
    return lineStr
        .split("|")
        .map((item, index) => {
            let obj = {};
            obj[`timeInit${index + 1}`] = item;
            return obj;
        })
        .reduce((acc, item) => {
            return Object.assign(acc, item);
        }, {});
};

