const actions = {
    async getOPUSummary({ state, commit, rootState }, _cycleId) {
        try {
            const data = await opuService.getOPUSummary(_cycleId);

            commit("SET_OPU", {
                ...data,
                preBPLine: data.preBpLine,
                postBPLine: data.postBpLine,
                prePulseLine: data.prePLine,
                postPulseLine: data.postPLine
            });

            return Promise.resolve();
        } catch (error) {
            commit("SET_NEW_OPU", {
                cycleId: rootState.artCycle.selectedCycle.cycleId,
                patientId: rootState.artCycle.patient.id
            });
            return Promise.reject();
        }
    },

    async getPrimaryCycles({ state, rootState }) {
        let cycleDate = format(
            new Date(rootState.artCycle.selectedCycle.cycleDate),
            DATE_FORMAT_STRING
        );
        let cycles = await opuService.getPrimaryCycles(
            rootState.artCycle.selectedCycle.patientId,
            cycleDate
        );

        state.primaryCycles = cycles.map((item) => ({
            ...item,
            label: (item.simulated_cycle==0 ? '' : 'SIM ') + `${item.cycle_type} cycle ${item.cycleId} (${item.cycleDate})`
        }));
    },

    async saveSummary({ commit }, formData) {

        console.log(formData)
        try {
            const data = await opuService.saveOpuSummary({
                ...formData,
                mdSummary: formData.md,
                rnRna: formData.rnRna,
                preBpLine: formData.preBPLine,
                postBpLine: formData.postBPLine,
                prePLine: formData.prePulseLine,
                postPLine: formData.postPulseLine
            });
            return data;
        } catch (error) {
            return Promise.reject();
        }
    },

    async deleteOpu({ state, rootState, commit }) {
        try {
            const data = opuService.deleteOpu(state.opuId);
            return Promise.resolve();
        } catch (err) {
            console.log("opu err");
            return Promise.reject();
        }
    }
};

