import { $axios } from "./app-http";

const GET_PROVIDERS_ENDPOINT = "provider/list";
const GET_SEMEN_CRYO_STORAGE_ENDPOINT = "semen-cryo-storage/get-header";
const SAVE_SEMEN_CRYO_STORAGE_ENDPOINT = "semen-cryo-storage/save-header";

export async function getProviders() {
    const {
        data: { data }
    } = await $axios.get(GET_PROVIDERS_ENDPOINT);
    return data;
}

export async function getSemenCryoStorage(cycleId) {
    const {
        data: { semenCryoStorageHeader }
    } = await $axios.get(GET_SEMEN_CRYO_STORAGE_ENDPOINT, {
        params: { cycleId }
    });
    return semenCryoStorageHeader;
}

export async function save(payload) {
    const { data } = await $axios.post(
        SAVE_SEMEN_CRYO_STORAGE_ENDPOINT,
        payload
    );
    return data;
}

export async function deleteSemenCryoStorage(ecId) {
    const { data } = await $axios.post("semen-cryo-storage/delete-header", {
        ecId
    });
    return data;
}
