import {
    createDaignosis,
    getDaignosisList,
    getUserGroup,
    getUserGroups,
    saveUserGroup,
    updataDaignosis,
    saveUserGroupPermission,
    getAppKeyList,
    getUserGroupPermissions
} from "@/service/maintanance";

const state = () => {
    return {
        diagnosisList: [],
        appKeyList: []
    };
};

const actions = {
    async getDaignosisList({ state }) {
        try {
            const { diagnosisInventoryList } = await getDaignosisList();
            state.diagnosisList = diagnosisInventoryList;
            return Promise.resolve();
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async createDaignosisInventory({ dispatch }, payload) {
        try {
            const data = await createDaignosis(payload);
            dispatch("getDaignosisList");
            return Promise.resolve();
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async updateDaignosisInventory({ dispatch }, payload) {
        try {
            const data = await updataDaignosis(payload.id, payload);
            dispatch("getDaignosisList");
            return Promise.resolve();
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async getUserGroup(_, id) {
        try {
            const { apiGroups } = await getUserGroup(id);
            return apiGroups;
        } catch (err) {
            return Promise.reject();
        }
    },
    async getUserGroups() {
        try {
            const { apiGroupList } = await getUserGroups();
            return apiGroupList;
        } catch (err) {
            return Promise.reject();
        }
    },
    async saveUserGroup(_, payload) {
        try {
            await saveUserGroup(payload);
            return Promise.resolve();
        } catch (err) {
            return Promise.reject();
        }
    },
    async getUserGroupPermissions(_, groupKey) {
        try {
            const { apiGroupPermission } = await getUserGroupPermissions(
                groupKey
            );
            return apiGroupPermission;
        } catch (err) {}
    },
    async saveUserGroupPermission(_, payload) {
        try {
            const data = await saveUserGroupPermission(payload);
            return data;
        } catch (err) {}
    },
    async getAppKeyList({ state }) {
        try {
            if (state.appKeyList.length == 0) {
                const { appKeyList } = await getAppKeyList();
                state.appKeyList = appKeyList;
            }
            return Promise.resolve();
        } catch (err) {
            return Promise.reject();
        }
    }
};

export default {
    namespaced: true,
    state,
    actions
};
