import * as doctorService from "@/service/doctor";

const state = () => ({
    doctors: [],
    embryologists: [],
    nurses: [],
});

const actions = {
    async getDoctorList({ state }) {
        try {
            if (state.doctors.length == 0) {
                const data = await doctorService.getDoctors();
                state.doctors = data;
            }
            return Promise.resolve();
        } catch (err) {
            return Promise.reject();
        }
    },
    async getEmbryologist({ state }) {
        try {
            if (state.embryologists.length == 0) {
                const { embyoLogists, nurses } = await doctorService.getEmbryologist();
                state.embryologists = embyoLogists;
                state.nurses = nurses;
            }
            return Promise.resolve();
        } catch (err) {
            return Promise.reject();
        }
    }
};

export default {
    namespaced: true,
    state,
    actions
};
