import * as cryoStorageService from "@/service/cyro-storage";

const state = () => ({
    list: [],
    receivedEmbryos: [],
    fertilizationLines: [],
    pugColorList: null,
    cassetteTypeList: null,
    cassetteColorList: null,
    dewarList: null
});
const actions = {
    async getCryoStorageList({ state }, cycleId) {
        try {
            const data = await cryoStorageService.getCryoStorageList(cycleId);
            state.receivedEmbryos = data;
            return Promise.resolve();
        } catch (err) {
            return Promise.reject();
        }
    },
    async getCyroStorageByPatient({ state }, patientId) {
        try {
            const data = await cryoStorageService.getCyroStorageByPatient(
                patientId
            );
            state.list = data;
            return Promise.resolve();
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async getFertilizationLineByCycle({ state }, cycleId) {
        try {
            const data = await cryoStorageService.getFertilizationLineByCycle(
                cycleId
            );
            state.fertilizationLines = data;
            return Promise.resolve();
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async saveFreezingLine({ dispatch, rootState }, payload) {
        try {
            const data = await cryoStorageService.saveFreezingLine(payload);
            dispatch(
                "getCyroStorageByPatient",
                rootState.artCycle.selectedCycle.patientId
            );
            dispatch(
                "getCryoStorageList",
                rootState.artCycle.selectedCycle.cycleId
            );
            return data;
        } catch (err) {
            return Promise.reject();
        }
    },
    async editFreezingLine({ dispatch, rootState }, payload) {
        try {
            const data = await cryoStorageService.editFreezingLine(payload);
            return Promise.resolve(data);
        } catch (err) {
            return Promise.reject();
        }
    },
    async deleteFreezingLine({ dispatch, rootState }, payload) {
        try {
            const data = await cryoStorageService.deleteFreezingLine(payload);
            dispatch(
                "getCyroStorageByPatient",
                rootState.artCycle.selectedCycle.patientId
            );
            dispatch(
                "getCryoStorageList",
                rootState.artCycle.selectedCycle.cycleId
            );
            return data;
        } catch (err) {
            return Promise.reject();
        }
    },
    async getCryoStorageOptions({ state }) {
        if (
            state.pugColorList ||
            state.cassetteTypeList ||
            state.cassetteColorList ||
            state.dewarList
        ) {
            return;
        }

        try {
            const {
                pugColorList,
                cassetteTypeList,
                cassetteColorList,
                dewarList
            } = await cryoStorageService.getCryoStorageOptions();

            state.pugColorList = pugColorList.map((item) => ({
                value: item.plugColor,
                label: item.plugColor
            }));
            state.cassetteTypeList = cassetteTypeList.map((item) => ({
                value: item.cassetteType,
                label: item.cassetteType
            }));
            state.cassetteColorList = cassetteColorList.map((item) => ({
                value: item.cassetteColor,
                label: item.cassetteColor
            }));
            state.dewarList = dewarList;
        } catch (err) {}
    },
    async getCryoStorageListFiltered({ state }, params) {
        try {
            const data = await cryoStorageService.getCryoStorageListFiltered(
                params
            );
            return data;
        } catch (err) {
            return Promise.reject();
        }
    }
};
const getters = {};
const mutations = {};

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
};
