import { PDF_REPORT_BASE, TIME_0 } from "@/constants";
import axios from "axios";
import { $axios } from "./app-http";
import { serialize } from "php-serialize";
import { format } from "date-fns";

export async function getBornOptions() {
    const { data } = await $axios.get("born-options");
    return data;
}

export async function getBornExtraOptions() {
    const { data } = await $axios.get("born/extra-options");
    return data;
}

export function buildPdfUrl(reportName, params) {
    return `${PDF_REPORT_BASE}?report_name=${reportName}&report_type=pdf&report_params=${encodeURIComponent(
        serialize(params)
    )}`;
}

export const cryoStorageStyle = (color) => {
    if (color == null || color == undefined) {
        return {};
    }

    let colorName = color.toLowerCase();

    if (colorName == "white" || colorName == "yellow") {
        return {
            backgroundColor: colorName,
            color: "black"
        };
    }

    return {
        backgroundColor: colorName,
        color: "white"
    };
};

export const clearPaitentAndCycle = () => {
    localStorage.removeItem("PATIENT");
    localStorage.removeItem("PARTNER");
    localStorage.removeItem("CYCYLE_SUMMARY");
    localStorage.removeItem("SELECTED_CYCLE");
};

export const showFormatedDate = (dateString) => {
    if (dateString == null) return "";
    const date = new Date(dateString + TIME_0);
    return format(date, "MM/dd/yy");
};
