import { $axios } from "./app-http";

const SEMEN_LIST_ENDPOINT = "semen/list";
const SEMEN_UPDATE_ENDPOINT = "semen/update-semen";
const SEMEN_CREATE_ENDPOINT = "semen/create-semen";
const SEMEN_DELETE_ENDPOINT = "semen/delete-semen";

export async function getSemenList(patient) {

    console.log(patient);
    const { data } = await $axios.get(SEMEN_LIST_ENDPOINT, {
        params: patient
    });

    return data;
}

export async function updateSemen(semen) {
    const { data } = await $axios.post(SEMEN_UPDATE_ENDPOINT, semen);
    return data;
}

export async function createSemen(semen) {
    const { data } = await $axios.post(SEMEN_CREATE_ENDPOINT, semen);
    return data;
}

export async function delteSemen(semen) {
    const { data } = await $axios.post(SEMEN_DELETE_ENDPOINT, semen);
    return data;
}
