<template>
    <p-block-ui :blocked="loading" :fullScreen="true">
        <p-progress-spinner v-show="loading" />
    </p-block-ui>

    <p-toast />
    <p-confirm-dialog />
    <p-confirm-popup group="popup" />

    <p-toolbar class="p-text-bold" v-if="!isMobile">
        <template #left>
            <header-info />
        </template>

        <template #right>
            <message-nofication />

            <p-button
                icon="pi pi-user"
                class="p-mr-2 p-ml-2"
                @click="showUserOverlay"
            />
        </template>
    </p-toolbar>
    <p-toolbar class="p-text-bold" v-else>
        <template #left>
            <header-info-mobile :menu="menu" />
        </template>

        <template #right>
            <message-nofication />

            <p-button
                icon="pi pi-user"
                class="p-mr-2 p-ml-2"
                @click="showUserOverlay"
            />
        </template>
    </p-toolbar>

    <p-tab-menu
        v-if="!isMobile"
        :activeIndex="parentActive"
        style="font-size: 0.9em;"
        :model="menu"
    />

    <div id="main" class="p-input-filled">
        <router-view />
    </div>

    <p-overlay-panel ref="userOverlay" style="width: 300px">
        <div class="p-grid">
            <div class="p-col-fixed" style="width:50px">
                <p-avatar icon="pi pi-user" />
            </div>
            <div class="p-col">
                <strong>{{ user.name }}</strong>
                <br />
                <strong>{{ user.username }}</strong> <br />
                <strong>{{ user.email }}</strong> <br />
            </div>
        </div>
        <p-divider />
        <div class="p-d-flex p-jc-between">
            <p-button label="Change Password" @click="changePassword" />
            <p-button
                label="Log Out"
                class="p-button-warning"
                icon="pi pi-sign-out"
                @click="logout()"
            />
        </div>
    </p-overlay-panel>

    <p-dialog
        v-model:visible="anotherPatientDialogue"
        modal
        header="Different patient"
        :style="{ width: '30rem' }"
    >
        <p>Go to Patient {{ anotherPatientData.patientId }}</p>
        <p-button label="No" class="p-mr-2" @click="cancel" />
        <p-button label="Yes" class="p-button-danger" @click="goForPatient" />
    </p-dialog>
</template>

<script>
import { computed, onBeforeUnmount, onMounted, provide, ref } from "vue";
import { useStore } from "vuex";
import { emitter, EVENT_STRINGS } from "@/event-bus";

import router from "@/router";
import beepSound from "@/assets/pos.mp3";
import buzzSound from "@/assets/neg.mp3";
import HeaderInfo from "@/components/shared/header-info.vue";
import HeaderInfoMobile from "@/components/shared/header-info-mobile.vue";
import MessageNofication from "@/components/shared/message-nofication.vue";
import mitt from "mitt";

export default {
    components: {
        HeaderInfo,
        HeaderInfoMobile,
        MessageNofication
    },
    setup() {
        const isMobile = ref(false);
        const store = useStore();
        const barcode = ref("");
        const barcodeTime = ref(null);
        const loading = ref(false);
        const patient = computed(() => store.state.artCycle.patient);
        const isFemalePatient = computed(
            () => store.state.artCycle.patient.sex !== "M"
        );

        const parnetActive = ref(0);

        const checkMobile = () => {
            isMobile.value = window.innerWidth <= 768; // Adjust the breakpoint as needed
        };

        const playBeep = (_flag) => {
            if (_flag == 1) {
                const audio = new Audio(beepSound);
                audio.play();
            } else {
                const audio = new Audio(buzzSound);
                audio.play();
            }
        };

        const openOtherPatientNavDialog = (_data) => {
            anotherPatientData.value = _data;
            anotherPatientDialogue.value = true;
        };

        const hasPermission = (name) => {
            if (!store.state.user) return false;
            let permission = store.state.user.permissions.find(
                (item) => item.appKey == name
            );
            if (permission == undefined) return false;
            return permission.readPermission;
        };

        const handleBarcodeScan = (event) => {
            // Modify your barcode scanning logic here
            const elementType = event.target.tagName.toLowerCase();
            if (elementType === "input" || elementType === "textarea") {
                return;
            }

            if (barcodeTime.value !== null) {
                var seconds =
                    (new Date().getTime() - barcodeTime.value.getTime()) / 1000;
                if (seconds > 5) {
                    barcode.value = "";
                }
            }

            if (event.key === "Enter") {
                if (barcode.value != "") {
                    processBarcode(barcode.value);
                    barcode.value = "";
                    barcodeTime.value = null;
                }
            } else {
                barcode.value += event.key;
                barcodeTime.value = new Date();
            }
        };

        const processBarcode = (barcode) => {
            // Handle the scanned barcode here (e.g., display, store, or process it)
            // console.log("Scanned barcode:", barcode);

            // store.dispatch("artCycle/updateGLobalScannedBarcode", barcode);
            emitter.emit("cycles-ui:check-barcode", barcode);
        };

        const anotherPatientDialogue = ref(false);

        const cancel = () => {
            anotherPatientDialogue.value = false;
        };

        const anotherPatientData = ref(null);
        const goForPatient = () => {
            let queryParam = null;
            if (anotherPatientData.value.cycleId) {
                queryParam = {
                    patientId: anotherPatientData.value.patientId,
                    to: anotherPatientData.value.scanarea
                };
            } else {
                queryParam = {
                    patientId: anotherPatientData.value.patientId,
                    to: anotherPatientData.value.scanarea,
                    cycleId: anotherPatientData.value.cycleId
                };
            }

            router.push({
                name: "redirect",
                query: queryParam
            });
            anotherPatientDialogue.value = false;
        };

        const menuArray = [
            {
                to: "/art-cycle",
                label: "Cycles",
                visible: hasPermission("art-cycle")
            },
            {
                to: "/cycle-monitoring",
                label: "Monitoring",
                visible: hasPermission("cycle-monitoring") && isFemalePatient
            },
            {
                to: "/daily-report",
                label: "Daily Rpt"
            },
            {
                to: "/blood-tests",
                label: "Blood",
                visible: hasPermission("blood-tests")
            },

            {
                to: "/bhcg",
                label: "BhCG",
                visible: hasPermission("bhcg")
            },
            { to: "/semen", label: "Semen", visible: hasPermission("semen") },
            {
                to: "/opu",
                label: "OPU",
                visible: hasPermission("opu") && isFemalePatient
            },
            {
                to: "/fertilization",
                label: "Fert",
                visible: hasPermission("fertilization")
            },
            {
                to: "/embryo-transfer",
                label: "Transfer",
                visible: hasPermission("embryo-transfer") && isFemalePatient
            },
            {
                to: "/pregnancy",
                label: "Pregnancy",
                visible: hasPermission("pregnancy") && isFemalePatient
            },

            {
                to: "/cryo-storage",
                label: "Cryo",
                visible: hasPermission("cryo-storage") && isFemalePatient
            },

            {
                to: "/cycle-report",
                label: "Report",
                visible: hasPermission("cycle-report")
            },
            {
                to: "/patient-message-log",
                label: "Messages",
                visible: hasPermission("patient-message-log")
            },
            {
                to: "/message-inbox",
                label: "Inbox",
                visible: hasPermission("message-inbox")
            },
            {
                to: "/maintanance",
                label: "Maint",
                visible: hasPermission("maintanance")
            },
            {
                to: "/consent-forms",
                label: "Consents",
                visible: hasPermission("consent-forms")
            },
            {
                to: "/barcodes",
                label: "Barcodes"
            },
            {
                to: "/freezer",
                label: "Freezer",
                visible: hasPermission("freezer")
            },
            {
                to: "/third-party",
                label: "Third Party"
            },
            {
                to: "/lab-mon",
                label: "Lab Mon"
            },
            {
                to: "/new-kpi",
                label: "KPI",
                visible: hasPermission("freezer")
            }
        ];

        const menu = ref(menuArray);

        const userOverlay = ref();
        const showUserOverlay = (event) => {
            userOverlay.value.toggle(event);
        };

        const user = computed(() => store.state.user);

        const logout = () => {
            store.dispatch("logout").then(() => {
                router.push({ name: "Login" });
            });
        };

        emitter.on(EVENT_STRINGS.LOGOUT, (data) => {
            console.log("logout from defalut");
        });

        emitter.on("app:show-page-loading", (val) => {
            loading.value = val;
        });

        onMounted(() => {
            store.dispatch("options/getBornOptions");
            store.dispatch("doctor/getEmbryologist");
            store.dispatch("doctor/getDoctorList");
            store.dispatch("artCycle/getCycleFormFieldOptions");

            store.dispatch("doctor/getDoctorList");

            document.addEventListener("keypress", handleBarcodeScan);
            emitter.on("cycles-ui:play-beep", playBeep);
            emitter.on("cycles-ui:redict-to-other", openOtherPatientNavDialog);

            isMobile.value = window.innerWidth <= 768; // Adjust the breakpoint as needed
            // Update the mobile state when window is resized
            window.addEventListener("resize", checkMobile);
        });

        onBeforeUnmount(() => {
            document.removeEventListener("keypress", handleBarcodeScan);
            emitter.off("cycles-ui:play-beep", playBeep);
            emitter.off("cycles-ui:redict-to-other", openOtherPatientNavDialog);

            window.removeEventListener("resize", checkMobile);
        });

        return {
            anotherPatientData,
            anotherPatientDialogue,
            cancel,
            goForPatient,
            menu,
            parnetActive,
            menuArray,
            loading,
            userOverlay,
            showUserOverlay,
            logout,
            isMobile,
            checkMobile,
            user,
            changePassword: () => router.push({ path: "/change-password" })
        };
    }
};
</script>

<style scoped>
.p-toolbar {
    margin: -8px;
    margin-top: 0;
    padding: 2px;
    background-color: white;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1000;
}

.p-tabmenu {
    margin: 0 -8px 0 -8px;
    width: 100%;
    position: fixed;
    top: 34px;
    background-color: white;
    z-index: 1000;
}

.p-progress-spinner {
    position: absolute;
    top: 300px;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1000;
}

@media only screen and (max-width: 600px) {
    #main {
        background-color: 150px;
    }
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {
    #main {
        margin-top: 115px;
    }
}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
    #main {
        margin-top: 115px;
    }
}

/* Large devices (laptops/desktops, 992px and up) */
/* 1378px */
@media only screen and (min-width: 992px) {
    #main {
        margin-top: 115px;
    }
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1435px) {
    #main {
        margin-top: 70px;
    }
}
</style>
