import * as embryoTransferService from "@/service/embryo-transfer";

const state = () => ({
    daysPostORList: [],
    catheterList: [],
    transferredEmbryos: []
});

const actions = {
    async getEmbryoTranasfer({ state }, cycleId) {
        try {
            const data = await embryoTransferService.getEmbryoTranasfer(
                cycleId
            );
            return data;
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async getFeildOptions({ state }) {
        try {
            if (state.daysPostORList.length == 0) {
                const daysPostORList = await embryoTransferService.getDaysPostORList();
                state.daysPostORList = daysPostORList;
            }
            if (state.catheterList.length == 0) {
                const catheterList = await embryoTransferService.getCatheterList();
                state.catheterList = catheterList;
            }
            return Promise.resolve();
        } catch (err) {
            return Promise.reject();
        }
    },
    async updateEmbryoTransfer({ state }, payload) {
        try {
            const data = embryoTransferService.updateEmbryoTrasfer(payload);
            return Promise.resolve();
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async createEmbryoTransfer({ state }, payload) {
        try {
            const data = embryoTransferService.createEmbryoTransfer(payload);
            return Promise.resolve();
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async deleteEmbryoTranfer({ rootState }) {
        try {
            const data = await embryoTransferService.deleteEmbryoTranfer(
                rootState.artCycle.selectedCycle.cycleId
            );
            Promise.resolve();
        } catch (err) {
            Promise.reject();
        }
    },
    async getTransferredEmbryos({ state }, cycleId) {
        try {
            const {
                embryoTransfered
            } = await embryoTransferService.getTransferredEmbryos(cycleId);
            state.transferredEmbryos = embryoTransfered;
            return Promise.resolve();
        } catch (err) {
            return Promise.reject();
        }
    }
};

export default {
    namespaced: true,
    state,
    actions
};
