import { createRouter, createWebHashHistory } from "vue-router";
import DefaultLayout from "@/components/layouts/defalut.vue";
import authService from "@/service/auth";
import store from "@/store";


const hasPermission = (name) => {
    if (!store.state.user) return false;
    let permission = store.state.user.permissions.find(
        (item) => item.appKey == name
    );
    if (permission == undefined) return false;
    return permission.readPermission;
};

function redirectToFallback(next) {
    next({ name: 'Unauthorize' })
}

