import { $axios } from "./app-http";

export async function getDaignosisList() {
    const { data } = await $axios.get("diagnosis-inventory/list");
    return data;
}

export async function createDaignosis(payload) {
    const { data } = await $axios.post("diagnosis-inventory/create", payload);
    return data;
}

export async function updataDaignosis(id, payload) {
    const { data } = await $axios.post(
        `diagnosis-inventory/update/${id}`,
        payload
    );
    return data;
}

export async function getUserGroup(id) {
    const { data } = await $axios.get(`api-groups/${id}`);
    return data;
}

export async function getUserGroups() {
    const { data } = await $axios.get("api-groups/list");
    return data;
}

export async function saveUserGroup(payload) {
    const { data } = await $axios.post("api-groups/save", payload);
    return data;
}

export async function getUserGroupPermissions(groupKey) {
    const { data } = await $axios.get(`api-groups-permission/${groupKey}`);
    return data;
}

export async function saveUserGroupPermission(payload) {
    const { data } = await $axios.post("api-groups-permission/save", payload);
    return data;
}

export async function getAppKeyList() {
    const { data } = await $axios.get("api-groups-permission/app-keys");
    return data;
}
