<template>
    <p-button class="p-button-sm" icon="pi pi-bars" @click="showMenu()" />
    <span class="p-ml-2 p-mr-2"> C: </span>
    <span class="info-text">{{ cycle ? cycle.cycleIdDisp : "" }}</span>
    <p-divider layout="vertical" />
    <div v-tooltip.bottom="parterTooltip">
        <span class="p-mr-2"> P: </span>
        <span class="info-text">
            {{ patient.chart ? patient.chart : "---" }}
        </span>
    </div>

    <p-sidebar
        v-model:visible="visible"
        header="Sidebar"
        class="w-full md:w-20rem lg:w-30rem"
    >
        <div class="header-info-container">
            <div class="p-mb-2">
                <span class="p-mr-2"> Cycle: </span>
                <span class="info-text">{{
                    cycle ? cycle.cycleIdDisp : "--------"
                }}</span>
                , &nbsp;&nbsp;
                <span class="info-text">{{
                    cycle ? cycle.cycleType : "-----"
                }}</span>
            </div>

            <div class="p-mb-2">
                <span class="p-mr-1">Patient No: </span>
                <span class="info-text">
                    {{ patient.chart ? patient.chart : "--------" }}
                </span>
            </div>

            <div v-tooltip.bottom="parterTooltip" class="p-mb-2">
                <span class="p-mr-2"> Name: </span>
                <span class="info-text">
                    {{ patient.firstName ? patient.firstName : "----" }}
                    {{ patient.lastName ? patient.lastName : "----" }}
                </span>
            </div>

            <div v-tooltip.bottom="parterTooltip" class="p-mb-2">
                <span class="info-text">
                    {{ sex }}
                </span>
                , &nbsp;&nbsp;
                <span class="info-text">
                    {{ patient.year ? patient.year : "----" }}/{{
                        patient.month ? patient.month : "--"
                    }}/
                    {{ patient.day ? patient.day : "--" }}
                </span>
                &nbsp;
                <span class="info-text"
                    >({{ patient ? patient.age : "--" }} yrs)</span
                >
            </div>

            <div v-tooltip.bottom="parterTooltip" class="p-mb-2">
                <span class="p-mr-2"> Ph: </span>
                <span class="info-text">
                    {{ patient.phone ? patient.phone : "----" }}
                </span>
            </div>

            <div class="p-mb-2">
                <span class="p-mr-1">Doctor: </span>
                <span class="info-text">{{
                    patient ? patient.doctor : "--"
                }}</span>
            </div>
            <div class="p-mb-2">
                <span style="color: #2196F3" v-if="isBornCycle">Born</span>
            </div>
            <!-- <p-button
                outlined
                v-if="patient.chart"
                label="Encounter"
                @click="openPatientEncounter"
            /> -->
            <div class="p-mb-6">
                <span class="p-buttonset" style="display: flex;">
                    <p-button
                        label="Encounter"
                        class="p-button-sm"
                        @click="openPatientEncounter('IN')"
                    />
                    <p-button
                        class="p-button-sm"
                        icon="pi pi-globe"
                        @click="openPatientEncounter('OUT')"
                    />
                </span>
            </div>
        </div>

        <div class="overflow-y-auto">
            <ul class="list-none p-0 m-0 overflow-hidden">
                <!-- Use v-for to iterate over the menu array -->

                <li v-for="(item, index) in menuItemsRef" :key="index">
                    <!-- Conditionally render the item based on its visibility -->
                    <a
                        class="menu-item flex align-items-center cursor-pointer p-3 border-round text-700 hover:surface-100 transition-duration-150 transition-colors p-ripple"
                        :style="{ fontSize: menuFontSize }"
                        @click="goTo(item.to)"
                    >
                        <!-- Render the label of the item -->
                        <span class="font-medium">{{ item.label }}</span>
                    </a>
                </li>
            </ul>
        </div>
    </p-sidebar>
</template>

<script>
import { computed, ref, toRefs } from "vue";
import { useRoute, useRouter } from "vue-router";
import { useStore } from "vuex";
import HeaderInfo from "@/components/shared/header-info.vue";
export default {
    components: {
        HeaderInfo
    },
    props: {
        menu: Array // Define the prop type as an array
    },
    setup(props) {
        const menuItemsRef = props.menu;
        const router = useRouter();

        // Create a computed property to access the value of the ref array
        const menuItems = computed(() => menuItemsRef.value);

        const menuFontSize = computed(() => (visible.value ? "20px" : "14px"));

        const store = useStore();

        const cycle = computed(() => store.state.artCycle.selectedCycle);
        const patient = computed(() => store.state.artCycle.patient);

        const visible = ref(false);
        const isBornCycle = computed(
            () => store.getters["artCycle/isBornCycle"]
        );

        const openPatientEncounter = (locality) => {
            if (locality == "IN") {
                const url =
                    "https://172.16.0.4/oscar/oscarEncounter/IncomingEncounter.do?" +
                    `providerNo=${
                        patient.value.doctorId
                    }&providerName=${encodeURIComponent(
                        patient.value.doctor
                    )}&demographicNo=${patient.value.chart}`;

                window.open(url);
            } else {
                const url =
                    "https://newlife.kai-oscar.com/oscar/oscarEncounter/IncomingEncounter.do?" +
                    `providerNo=${
                        patient.value.doctorId
                    }&providerName=${encodeURIComponent(
                        patient.value.doctor
                    )}&demographicNo=${patient.value.chart}`;

                window.open(url);
            }
        };

        const showMenu = () => {
            console.log(menuItems);
            visible.value = true;
        };

        const sex = computed(() => {
            if (store.state.artCycle.patient == null) {
                return "-";
            } else if (store.state.artCycle.patient.sex === "F") {
                return "Female";
            } else if (store.state.artCycle.patient.sex === "M") {
                return "Male";
            } else {
                return store.state.artCycle.patient.sex;
            }
        });
        const parterTooltip = computed(() => {
            let partner = store.state.artCycle.partner;

            if (partner) {
                return `Partner: <strong>${partner.firstName} ${partner.lastName}</strong>
                    Age: <strong>${partner.age}<strong>
                `;
            } else {
                return "Parner Not Available";
            }
        });

        const goTo = (toPath) => {
            router.push({ path: toPath });
            visible.value = false;
        };

        return {
            cycle,
            menuItems,
            menuItemsRef,
            menuFontSize,
            patient,
            isBornCycle,
            parterTooltip,
            visible,
            showMenu,
            goTo,
            sex,
            openPatientEncounter
        };
    }
};
</script>

<style scoped>
span {
    font-size: 0.9em;
    font-weight: 500;
}
.info-text {
    color: #800000;
}

.list-none {
    list-style: none;
    /* Removes the default bullet points from the list */
}

.p-0 {
    padding: 0;
    /* Sets padding to 0 */
}

.m-0 {
    margin: 0;
    /* Sets margin to 0 */
}

.overflow-hidden {
    overflow: hidden;
    /* Hides any content that overflows the container */
}

.flex {
    display: flex;
    /* Makes the element a flex container */
}

.align-items-center {
    align-items: center;
    /* Aligns flex items along the cross-axis (vertically) */
}

.cursor-pointer {
    cursor: pointer;
    /* Changes the cursor to a pointer on hover, indicating interactivity */
}

.border-round {
    border-radius: 0.25rem;
    /* Applies a border radius of 0.25rem (adjust as needed) */
}

.text-700 {
    font-weight: 700;
    /* Sets font weight to 700 (bold) */
}

.hover:surface-100:hover {
    /* This seems to be a custom class for handling hover effects, specific to your project */
}

.transition-duration-150 {
    transition-duration: 150ms;
    /* Sets the duration for CSS transitions to 150 milliseconds */
}

.transition-colors {
    transition-property: color;
    /* Specifies that color changes should be transitioned */
}

.p-ripple {
    /* This seems to be a custom class for creating ripple effects on click, specific to your project */
}

.font-medium {
    font-weight: 500;
    /* Sets font weight to 500 (medium) */
}

.menu-item {
    font-weight: bold; /* Set font weight to bold */
    padding: 10px 6px;
    transition: color 0.3s, background-color 0.3s;
}

.menu-item:hover {
    color: white; /* Change font color on hover */
    background-color: #a8a8a8; /* Change background color on hover */
}

.header-info-container {
    max-width: 100%;
}

.vertical-line {
    border-left: 1px solid black; /* Adjust the color and thickness as needed */
    height: 1.5em; /* Adjust the height of the line as needed */
    margin-left: 10px; /* Adjust the spacing between the text and the line as needed */
}
</style>
