import { $axios } from "./app-http";

const GET_PROVIDERS_ENDPOINT = "provider/list";

export async function getProviders() {
    const {
        data: { data }
    } = await $axios.get(GET_PROVIDERS_ENDPOINT);
    return data;
}

export async function getLocationOption() {
    const {
        data: { data }
    } = await $axios.get("provider/site-list");
    return data;
}
