import { $axios } from "./app-http";

export async function getAllCycleType() {
    const {
        data: { allCycleTypes }
    } = await $axios.get("cycle-report/get-all-cycle-types");
    return allCycleTypes;
}

export async function getCycleRport(payload) {
    const {
        data: { cycleRecords }
    } = await $axios.post("cycle-report/get-cycle-records", {
        ...payload
    });
    return cycleRecords;
}

export async function getCycleKPIC(payload) {
    const {
        data: { gData }
    } = await $axios.post("cycle-report/get-cycle-pregnancy-kpi", {
        ...payload
    });
    return gData;
}

